import { PomodoroService } from '@src/pomodoro/PomodoroService'
import Browser from 'webextension-polyfill'

export const V1PomodoroMigration = {
  version: '4.1.0',
  name: 'Pomodoro v4 settings migration',
  run: async function runMigration() {
    const { pomodoroModeEnabled } = await Browser.storage.sync.get(
      'pomodoroModeEnabled'
    )
    const { pomodoroSoundEnabled } = await Browser.storage.sync.get(
      'pomodoroSoundEnabled'
    )
    const { pomodoroSoundVolume } = await Browser.storage.sync.get(
      'pomodoroSoundVolume'
    )
    const { pomodoroInterval } = await Browser.storage.sync.get(
      'pomodoroInterval'
    )

    if (!pomodoroModeEnabled) return

    await PomodoroService.fillSettings()
    const settings = PomodoroService.getSettings()
    await PomodoroService.setStoredSettings({
      ...settings,
      isEnabled: pomodoroModeEnabled,
      focusDuration: pomodoroInterval * 60,
      isAlarmEnabled: pomodoroSoundEnabled,
      alarmVolumePercent: pomodoroSoundVolume,
    })
  },
}
