import { Db } from '@src/lib/db'
import { logger } from '@src/lib/errorReport'
import { stopRunningTimeEntry } from '@src/lib/timeEntries'
import { CurrentTEService } from '@src/v4/services/CurrentTEService'
import { NotificationService } from '@src/v4/services/NotificationService'
import { TEListService } from '@src/v4/services/TEListService'
import { DateTime } from 'luxon'
import { type MessageFrom } from 'src/typdef'
import * as Browser from 'webextension-polyfill'

import { type TimeEntryRequest } from '@toggl/track-types'

import * as timeEntryBackgroundService from './_internal/timeEntry'

const from: MessageFrom = { type: 'popup' }

export const configure = () => {
  Browser.windows.onRemoved.addListener(async function () {
    try {
      const stopAutomatically = await Db.get('stopAutomatically')
      if (!stopAutomatically) return

      const windows = await Browser.windows.getAll()
      if (windows.length <= 0) {
        const current = await CurrentTEService.retrieve()

        if (current) {
          await stopRunningTimeEntry(current, null)
        }
      }
    } catch (error) {
      logger.error(error)
      await NotificationService.create(
        'Error stopping time entry',
        error.message
      )
    }
  })

  Browser.windows.onCreated.addListener(async () => {
    try {
      const windows = await Browser.windows.getAll()

      if (windows.length > 1) {
        return false
      }

      if (!(await Db.get('startAutomatically'))) {
        return false
      }

      const TEs = await TEListService.invoke({
        store: true,
      })

      const TEsList = Object.values(TEs)
      const lastTE = TEsList.pop()

      if (!lastTE || lastTE.duration < 0) return

      const start = DateTime.now()

      await timeEntryBackgroundService.start(
        {
          ...lastTE,
          start,
          duration: -parseInt((start.toMillis() / 1000).toFixed(0), 10),
          stop: undefined,
          tag_ids: lastTE.tag_ids ?? [],
        } as unknown as TimeEntryRequest,
        from
      )
    } catch (error) {
      logger.error(error)
      await NotificationService.create('Something went wrong', error.message)
    }
  })
}
