import apiClient from '@src/background/_internal/_api'
import { logger } from '@src/lib/errorReport'
import { useMutation } from '@tanstack/react-query'
import Browser from 'webextension-polyfill'

import { type Tag } from '@toggl/track-types'

import { useTagsListStorage } from './TagsListService'

type MutationContext = { name: string; workspaceId: number }

const TagsCreateService = {
  create: async (name: string, workspaceId: number) => {
    const { data } = await apiClient.tag.create(name, workspaceId)
    return data
  },

  dispatch(data: MutationContext): Promise<Tag> {
    return Browser.runtime.sendMessage({
      type: 'CREATE_TAG_V4',
      payload: data,
    })
  },
}

type MutationData = Awaited<ReturnType<typeof TagsCreateService['create']>>

const useTagsCreateMutation = () => {
  const [, setTagsList] = useTagsListStorage()

  return useMutation<MutationData, Error, MutationContext>(
    (data) => TagsCreateService.dispatch(data),
    {
      onError: (error) => {
        return logger.error(error)
      },

      // Unsure about if will be useful.
      onSuccess: (data) => {
        setTagsList((old) => {
          return { ...old, [data.id]: data }
        })
      },
    }
  )
}

export { TagsCreateService, useTagsCreateMutation }
