import Browser from 'webextension-polyfill'

const NotificationService = {
  eventKey: 'NOTIFY_USER_V4_E' as const,

  /**
   * Create a notification
   *
   * @param title Title of notification
   * @param message Message content
   * @param priority Priority ranges from -2 to 2. -2 is lowest priority. 2 is
   *   highest. Zero is default.
   */
  create(title: string, message: string, priority = 2) {
    return Browser.notifications.create({
      type: 'basic',
      iconUrl: Browser.runtime.getURL('images/icon-128.png'),
      title,
      message,
      priority,
    })
  },

  dispatch(title: string, message: string) {
    return Browser.runtime.sendMessage({
      type: NotificationService.eventKey,
      payload: { title, message },
    })
  },
}

export { NotificationService }
