import { type StateChangeListener } from '@src/lib/db'
import { logger } from '@src/lib/errorReport'
import { stopRunningTimeEntry } from '@src/lib/timeEntries'
import { CurrentTEService } from '@src/v4/services/CurrentTEService'

import { PomodoroService } from './PomodoroService'

export const onPomodoroV2StateChangesListener: StateChangeListener = async (
  changes,
  areaName
) => {
  try {
    if (areaName === 'local' && CurrentTEService.storageKey in changes) {
      const oldValue = changes[CurrentTEService.storageKey].oldValue
      const newValue = changes[CurrentTEService.storageKey].newValue
      const pomodoroState = PomodoroService.getState()
      // TEs -> Same, Duration -> Different -> Stop the TE and reset the pomodoro.
      if (oldValue?.id === newValue?.id) {
        const isDurationDifferent = oldValue?.duration !== newValue?.duration

        if (isDurationDifferent && pomodoroState?.isRunning) {
          await stopRunningTimeEntry(newValue, newValue.stop)
        }
      }
    }

    if ('Settings.Pomodoro' in changes && areaName === 'sync') {
      PomodoroService.reset()
      await PomodoroService.init({ force: true })
    }
  } catch (error) {
    logger.error(error, 'onPomodoroV2StateChangesListener')
  }
}
