Browser.permissions.onAdded.addListener((permissions) => {
  if (permissions.origins?.includes('<all_urls>')) {
    void Db.set(
      'integrations',
      Object.keys(TogglOrigins).reduce((acc, key) => {
        acc[key] = true
        return acc
      }, {})
    ).then(() => {
      void Db.set(onboardingConfig.storage, 'all_enabled')
    })
  }
})

Browser.tabs.onUpdated.addListener(
  async (
    tabId: number,
    changeInfo: Browser.Tabs.OnUpdatedChangeInfoType,
    tab: Browser.Tabs.Tab
  ) => {
    const tinyButtonEnabled = await Db.get('enableTinyButton')

    if (changeInfo.status === 'complete') {
      // This bug is only in chromium
      if (!isFirefox()) {
        await Browser.scripting
          .executeScript({
            target: { tabId },
            files: ['worker_proxy.js'],
          })
          .catch((e) => logger.error(e))
      }
      contextMenu.configure(tab)
      contentScript.configure(tabId, tab)

      if (tinyButtonEnabled) {
        tinyButton.configure(tabId, tab)
      }
    }
  }
)

Browser.runtime.onInstalled.addListener(async (details) => {
  logger.debug('SW::onInstalled')

  if (details.reason === 'install') {
    if (isFirefox()) {
      await Browser.tabs.create({
        url: Browser.runtime.getURL('src/pages/dataConsent/index.html'),
      })
    } else {
      await Browser.tabs.create({
        url: Browser.runtime.getURL('src/pages/postInstallLP/index.html'),
      })
    }
  }

  if (details.reason === 'update') {
    const needsToShowCL = await Db.get('showChangelog')

    // On the first update needsToShowCL will be undefined
    if (needsToShowCL || needsToShowCL === undefined) {
      await Browser.tabs.create({
        url: Browser.runtime.getURL('settings.html#/whats-new'),
      })
    }
  }
  await runMigration(details)
})

Browser.commands.onCommand.addListener(async (command) => {
  await initSentry()

  // TODO: Investigate on this, why do we need to wrap this in Sentry when we can just catch the error?
  Sentry.wrap(function () {
    if (command === 'quick-start-stop-entry')
      TEListService.retrieve()
        .then(async (entries) => {
          const entriesList = Object.values(entries ?? {}).sort((a, b) => {
            // Most recent entries first.
            if (a.start > b.start) return -1
            if (b.start > a.start) return 1
            return 0
          })

          if (entriesList[0]?.duration < 0) {
            await stopRunningTimeEntry(entriesList[0], null)
          } else {
            const start = new Date()
            const latestEntry = entriesList[0]

            const entry = {
              at: start.toISOString(),
              billable: false,
              description: latestEntry.description,
              duration: -parseInt((start.getTime() / 1000).toFixed(0), 10),
              start: start.toISOString(),
              tags: latestEntry.tags ?? [],
              tag_ids: latestEntry.tag_ids ?? [],
              project_id: latestEntry.project_id,
              workspace_id: latestEntry.workspace_id,
            } as unknown as TimeEntryRequest

            await timeEntryBackgroundService.start(entry, {
              type: 'contentScript',
            })
          }
        })
        .catch((error) => {
          logger.debug(error)
          NotificationService.create('Error', error.message)
        })
  })
})

// TODO: Same logic as full-fetch alarm, uniform this.
export const loadResources = async (forwardError = false) => {
  try {
    // No user? Throw an error.
    await UsersService.invoke({ store: true })

    return Promise.all([
      CurrentTEService.invoke({ store: true }),
      TagsListService.invoke(null),
      TasksListService.invoke(null),
      ClientsListService.invoke(null),
      ProjectsListService.invoke(null),
      WorkspacesListService.invoke(null),
      OrgFeaturesService.invoke(),

      // This one needs to be invoked with a workspace ID
      WSPreferencesService.invoke(
        (await SelectedWorkspaceService.retrieve()) ?? 0,
        {
          store: true,
        }
      ),
    ])
  } catch (error) {
    logger.debug(error) // TODO: Does this needs to go to Sentry as well? @with-shrey @askides
    if (forwardError) {
      throw error
    }
  }
}

const clearData = async () => {
  await Browser.storage.local.clear()
}

const isExtensionRunningInStaging =
  process.env.TOGGL_ACCOUNTS_HOST?.includes('toggl.space') ?? false

const verifyUser = async (userId: number, isStaging?: boolean) => {
  if (isStaging && !isExtensionRunningInStaging) {
    return
  }
  const user = await UsersService.retrieve()

  if (user?.id !== userId) {
    await clearData()
    await loadResources()
  }
}

Browser.runtime.onMessage.addListener(async (message: SWMessage) => {
  await initSentry()

  // Log the message for debugging purposes.
  logger.debug(`SW::onMessage -> at: ${Date.now()}`, message)

  switch (typeof message === 'object' ? message.type : '') {
    case UsersService.eventKey: {
      return UsersService.invoke({ store: true })
    }
    case UserUpdateService.eventKey: {
      return UserUpdateService.invoke(message.payload)
    }
    case CurrentTEService.eventKey: {
      return CurrentTEService.invoke({ store: true })
    }
    case TEListService.eventKey: {
      return TEListService.invoke({ store: true })
    }
    case TagsListService.eventKey: {
      return TagsListService.invoke(await TagsListService.since())
    }
    case TasksListService.eventKey: {
      return TasksListService.invoke(await TasksListService.since())
    }
    case ClientsListService.eventKey: {
      return ClientsListService.invoke(await ClientsListService.since())
    }
    case ProjectsListService.eventKey: {
      return ProjectsListService.invoke(await ProjectsListService.since())
    }
    case WorkspacesListService.eventKey: {
      return WorkspacesListService.invoke(await WorkspacesListService.since())
    }
    case OrgFeaturesService.eventKey: {
      return OrgFeaturesService.invoke()
    }
    case OrganizationsListService.eventKey: {
      return OrganizationsListService.invoke()
    }

    case ProjectsCreateService.eventKey: {
      return ProjectsCreateService.invoke(
        message.payload.data,
        message.payload.workspaceId
      )
    }
    case 'CREATE_TAG_V4': {
      return TagsCreateService.create(
        message.payload.name,
        message.payload.workspaceId
      )
    }
    case NotificationService.eventKey: {
      return NotificationService.create(
        message.payload.title,
        message.payload.message
      )
    }
    case UsersService.logoutKey: {
      return UsersService.logout()
    }
    case WSPreferencesService.eventKey: {
      return WSPreferencesService.invoke(message.payload.workspaceId, {
        store: true,
      })
    }

    case 'HEAP_EVENT_TRACK': {
      return heapClient.track(message.payload)
    }

    case 'start-entry':
      return timeEntryBackgroundService.start(
        message.payload.entry,
        message.payload.from
      )

    case 'update-entry':
      return timeEntryBackgroundService.update(
        message.payload.entry,
        message.payload.from
      )
    case 'delete-entry':
      return timeEntryBackgroundService.remove(message.payload)

    case 'activate':
      return loadResources(message?.payload?.forwardError)
    case 'VERIFY_USER':
      return verifyUser(message.payload.userId, message.payload.isStaging)
    case 'CLEAR_DATA':
      if (message.payload.isStaging && !isExtensionRunningInStaging) {
        return
      }
      return clearData()
    case 'testing':
      logger.debug('payload: ', message.payload)
      break
    default:
      logger.debug('nothing')
  }
})
