import apiClient from '@src/background/_internal/_api'
import { logger } from '@src/lib/errorReport'
import { useMutation } from '@tanstack/react-query'
import Browser from 'webextension-polyfill'

import { type Project, type ProjectCreateProperties } from '@toggl/track-types'

import { useProjectsListStorage } from './ProjectsListService'

type MutationContext = { data: ProjectCreateProperties; workspaceId: number }

const ProjectsCreateService = {
  eventKey: 'CREATE_PROJECT_V4' as const,

  invoke: async (payload: ProjectCreateProperties, wid: number) => {
    const { data } = await apiClient.project.create(wid, payload)
    return data
  },

  dispatch(data: MutationContext): Promise<Project> {
    return Browser.runtime.sendMessage({
      type: ProjectsCreateService.eventKey,
      payload: data,
    })
  },
}

type MutationData = Awaited<ReturnType<typeof ProjectsCreateService['invoke']>>

const useProjectsCreateMutation = () => {
  const [, setProjectsList] = useProjectsListStorage()

  return useMutation<MutationData, Error, MutationContext>(
    (data) => ProjectsCreateService.dispatch(data),
    {
      onError: (error) => {
        return logger.error(error)
      },

      onSuccess: (data) => {
        setProjectsList((old) => {
          return { ...old, [data.id]: data }
        })
      },
    }
  )
}

export { ProjectsCreateService, useProjectsCreateMutation }
