import { logger } from '@src/lib/errorReport'
import { stopRunningTimeEntry } from '@src/lib/timeEntries'
import { CurrentTEService } from '@src/v4/services/CurrentTEService'
import { NotificationService } from '@src/v4/services/NotificationService'
import { DateTime } from 'luxon'
import Browser from 'webextension-polyfill'

import { PomodoroService, type PomodoroState } from './PomodoroService'
import { playPomodoroAlert, startTicking, stopTicking } from './sounds'
import { getRemainingSessionTime } from './utils/getRemainingSessionTime'

/**
 * Pomodoro Service
 *
 * This code is responsible for handling the Pomodoro state and settings.
 */

Browser.runtime.onConnect.addListener((channel) => {
  if (channel.name === 'POPUP') {
    // Listen for messages from the popup
    channel.onMessage.addListener((message) => {
      if (message.type === 'Pomodoro.start') {
        PomodoroService.start()
      }

      if (message.type === 'Pomodoro.goToNextState') {
        PomodoroService.goToNextState()
      }
    })

    // Listen for disconnect
    channel.onDisconnect.addListener(() => {
      logger.debug('POPUP channel disconnected.')
    })
  }
})

// Init settings
PomodoroService.init()

// Listen to events
PomodoroService.on('clock:init', (state: PomodoroState) => {
  Browser.alarms.clear('Pomodoro.Alarm')

  try {
    if (state.isRunning) {
      const calcRemainingTime = getRemainingSessionTime(
        state.isRunning,
        state.duration
      )

      Browser.alarms.create('Pomodoro.Alarm', {
        when: Date.now() + calcRemainingTime * 1000,
      })
      if (!state.isBreak && !state.isLargeBreak)
        void startTicking(calcRemainingTime)
    }
  } catch (error) {
    logger.error(error)
  }
})

PomodoroService.on('clock:start', (state: PomodoroState) => {
  try {
    if (state.isRunning) {
      const calcRemainingTime = getRemainingSessionTime(
        state.isRunning,
        state.duration
      )

      Browser.alarms.create('Pomodoro.Alarm', {
        when: Date.now() + calcRemainingTime * 1000,
      })
      if (!state.isBreak && !state.isLargeBreak)
        void startTicking(calcRemainingTime)
    }
  } catch (error) {
    logger.error(error)
  }
})

PomodoroService.on('clock:reset', () => {
  Browser.alarms.clear('Pomodoro.Alarm')
  void stopTicking()
})

PomodoroService.on('state:change', async (state: PomodoroState) => {
  try {
    const settings = PomodoroService.getSettings()

    // Store the current state
    await Browser.storage.local.set({ 'Pomodoro.State': state })

    if (state.computed.isIdle && state.session > 1) {
      await NotificationService.create(
        'Back to Work!',
        `Let's dive into another focused session.`
      )
    }

    if (state.isBreak && !state.isRunning) {
      await NotificationService.create(
        'Take a Break!',
        `Time to rest and recharge for a bit.`
      )
    }

    if (state.isLargeBreak) {
      await NotificationService.create(
        'Long Break Time!',
        `Relax for ${
          settings.longBreakDuration / 60
        } minutes. You've earned it!`
      )
    }

    if (state.isBreak || state.isLargeBreak) {
      const current = await CurrentTEService.retrieve()

      if (current) {
        await stopRunningTimeEntry(
          current,
          DateTime.fromISO(current.start)
            .plus({ seconds: settings.focusDuration })
            .toISO(),
          {
            isManuallyStopped: false,
          }
        )
      }
    }

    if (
      state.computed.isIdleBreak ||
      (state.computed.isIdle && state.session > 1)
    ) {
      await playPomodoroAlert()
    }
  } catch (error) {
    logger.error(error)
  }
})
