import { Db } from '@src/lib/db'
import { logger } from '@src/lib/errorReport'
import { extractHostname, extractRootDomain } from '@src/lib/utils'
import * as Browser from 'webextension-polyfill'

import storedOrigins from '@toggl/track-extension'

const configureLogin = (tabId: number) => {
  Browser.scripting.executeScript({
    target: { tabId },
    files: ['./login.js'],
  })
}

const startIntegration = async (tabId: number, filename: string) => {
  // TODO: redux refactor => really needed?
  // await loadResources();
  logger.debug('adding content scripts')

  await Browser.scripting
    .insertCSS({
      target: { tabId },
      files: ['src/content/style.css'],
    })
    .catch((e) => logger.error(e))

  await Browser.scripting
    .executeScript({
      target: { tabId },
      files: ['src/content/index.js'],
    })
    .catch((e) => logger.error(e))

  Browser.scripting
    .executeScript({
      target: { tabId },
      files: [`src/content/${filename}`],
    })
    .catch((e) => logger.error(e))
}

export const configure = async (tabId, tab) => {
  if (tab.url === undefined) return
  try {
    const response = await Browser.tabs.sendMessage(tabId, { type: 'ping' })
    if (response.type === 'pong') return
  } catch (e) {
    logger.debug(e)
  }

  const rootDomain = extractRootDomain(tab.url!)
  // Host name check doesnt depend on integration origins
  const hostName = extractHostname(tab.url)
  if (hostName.includes('toggl.com') || hostName.includes('toggl.space')) {
    return configureLogin(tabId)
  }

  const integrations = await Db.get('integrations')
  const customIntegrations = await Db.get('customIntegrations')
  const customScripts = await Db.get('customScripts')
  const extractedHostname = extractHostname(tab.url!)
  if (
    (!integrations || !integrations[rootDomain]) &&
    !customIntegrations?.[extractedHostname] &&
    !customScripts?.[extractedHostname]
  )
    return

  // Probably this is no more useful as the storage is now reactive to instant changes, leaving a comment for now as a reminder i will remove it later.
  // await userBackgroundService.fetch().catch(e => logger.error(e));

  const integrationObj = storedOrigins[rootDomain]

  if (integrationObj) {
    const filename =
      integrationObj?.file ??
      `${integrationObj.name.toLocaleLowerCase().replace(' ', '-')}.js`

    if (integrations && integrations[rootDomain]) {
      startIntegration(tabId, filename)
    }
  } else if (
    customIntegrations?.[extractedHostname]?.name ||
    customIntegrations?.[extractedHostname]?.file
  ) {
    const filename =
      customIntegrations?.[extractedHostname]?.file ??
      `${customIntegrations[extractedHostname].name
        .toLocaleLowerCase()
        .replace(' ', '-')}.js`

    if (filename) startIntegration(tabId, filename)
  }
  if (customScripts?.[extractedHostname]) {
    const hostname = extractedHostname
    const filename =
      customScripts?.[hostname]?.file ??
      `${customScripts[hostname].name.toLocaleLowerCase().replace(' ', '-')}.js`
    if (filename) startIntegration(tabId, filename)
  }
}
