import { logger } from '@src/lib/errorReport'
import { CurrentTEService } from '@src/v4/services/CurrentTEService'
import Browser from 'webextension-polyfill'

import { type RequestError } from '@toggl/track-api'
import { type TimeEntryV9 } from '@toggl/track-types'

/**
 * Handle carious error that can happen while TE creation / update /deletion
 *
 * 403 - Since these endpoints are called independently from others its possible
 * that user has logged out or cleared cache
 *
 * 404 - there is no way to get rid of a running TE that starts giving 404 due
 * to duplicate calls or other client deleting it which is unlikely due to WSS
 * but possible in case of disconnection
 *
 * This needs to be invoked within SW/Background the error object is missing
 * additional context when it is transmitted to content script
 *
 * @param error - Error object from api call
 * @param timeEntry - Time entry on which op is being performed
 */
export async function handleTimeEntryEndpointError(
  error: RequestError,
  timeEntry?: TimeEntryV9
) {
  try {
    if (error.response?.status === 401 || error.response?.status === 403) {
      await Browser.storage.local.clear()
    } else if (error.response?.status === 404 && timeEntry) {
      const currentTE = await CurrentTEService.retrieve()
      if (currentTE?.id === timeEntry.id) {
        // Performing operation on a TE deleted using other client
        await CurrentTEService.clear()
      }
    }
  } catch (error) {
    logger.error(error, 'handleTimeEntryEndpointError')
  }
}
