import { onWebSocketDataReceived } from '@src/background/push-event-handlers'
import { type StateChangeListener } from '@src/lib/db'
import { logger } from '@src/lib/errorReport'
import {
  onWebsocketAuthenticated,
  onWebsocketDisconnected,
} from '@src/lib/fetch-handler'
import { isServiceWorker } from '@src/lib/utils'
import { UsersService } from '@src/v4/services/UsersService'
import Browser from 'webextension-polyfill'

export const WEBSOCKET_ALARM_ID = 'WEBSOCKET_ALARM'
export const WEBSOCKET_ALARM_RETRY_ID = 'WEBSOCKET_ALARM_RETRY'
export const HEALTH_CHECK_TIMEOUT_IN_MINUTE = 10 / 60
export const SILENCE_TIME_LIMIT = 20 * 1000
export const SHORT_WAIT_UNTIL_REOPEN = 5 * 1000
export const LONG_WAIT_UNTIL_REOPEN = 60 * 1000

const DEBUG = false

let socket: WebSocket
let lastAliveCheck: number | null = null

function updateKeepAlive() {
  lastAliveCheck = Date.now()
}

function getSessionCookie(): Promise<string | undefined> {
  return new Promise((resolve, reject) => {
    chrome.cookies.get(
      {
        url: `https://${process.env.TRACK_HOSTNAME}`,
        name: '__Secure-accounts-session',
      },
      (cookie) => {
        if (chrome.runtime.lastError) {
          reject(chrome.runtime.lastError)
        } else {
          resolve(cookie?.value)
        }
      }
    )
  })
}

async function onSocketOpen(event: MessageEvent) {
  logger.debug('WS::onSocketOpen', event)

  updateKeepAlive()
  let data = ''
  try {
    if (isServiceWorker()) {
      const token = await getSessionCookie()
      const authenticationMessage = {
        type: 'authenticate',
        auth_type: 'oauth',
        token,
      }
      data = JSON.stringify(authenticationMessage)
    } else {
      const authenticationMessage = {
        type: 'authenticate',
        auth_type: 'cookie',
      }
      data = JSON.stringify(authenticationMessage)
    }
    socket.send(data)
  } catch (e) {
    logger.error(e, 'Error authenticating websocket')
  }
}

const pong = () => {
  const data = JSON.stringify({ type: 'pong' })
  try {
    socket.send(data)
  } catch (e) {
    logger.error(e, 'Error ponging websocket server')
  }
}

function onSocketMessage(event: MessageEvent) {
  DEBUG &&
    logger.debug(
      'WebSocket message received:',
      event.data,
      Date.now(),
      new Date().toISOString()
    )

  updateKeepAlive()

  const data = JSON.parse(event.data)

  const isUsefulIncomingData = !!data.model
  const isConnectionConfirmation = !!data.session_id
  const isPing = !isConnectionConfirmation && !isUsefulIncomingData

  if (isUsefulIncomingData) {
    void onWebSocketDataReceived(data)
  } else if (isPing) {
    pong()
  } else if (isConnectionConfirmation) {
    onWebsocketAuthenticated()
  }
}

const onSocketError = (event: Event) => {
  logger.error(new Error(JSON.stringify(event)), 'Websocket unexpected error')
  onWebsocketDisconnected()
}

const onSocketClose = async () => {
  onWebsocketDisconnected()
  logger.debug('WS::onSocketClose')

  const retryMiliSeconds = lastAliveCheck
    ? SHORT_WAIT_UNTIL_REOPEN
    : LONG_WAIT_UNTIL_REOPEN

  try {
    // Schedule reconnection of websocket
    await Browser.alarms.clear(WEBSOCKET_ALARM_RETRY_ID)
    Browser.alarms.create(WEBSOCKET_ALARM_RETRY_ID, {
      when: Date.now() + retryMiliSeconds,
    })
  } catch (error) {
    logger.error(error, 'Error setting up retry alarm')
  }
}

async function isAuthenticated() {
  const user = await UsersService.retrieve()
  return !!user?.id
}

async function setupLongRetryAlarm() {
  try {
    await Browser.alarms.clear(WEBSOCKET_ALARM_RETRY_ID)
    Browser.alarms.create(WEBSOCKET_ALARM_RETRY_ID, {
      when: Date.now() + LONG_WAIT_UNTIL_REOPEN,
    })
  } catch (error) {
    logger.error(error, 'Error setting up authentication retry alarm')
  }
}

export async function connectWebSocket() {
  // No need to open new connection if already open
  if (
    socket?.readyState === WebSocket.OPEN ||
    socket?.readyState === WebSocket.CONNECTING
  )
    return
  try {
    // Don't need to maintain connection if no user is authenticated
    const isAuthenticatedUser = await isAuthenticated()
    if (!isAuthenticatedUser) {
      // Fail safe incase SW sleeps
      await setupLongRetryAlarm()
      return
    }

    lastAliveCheck = null
    socket = new WebSocket(`wss://${process.env.TRACK_HOSTNAME}/stream`)

    socket.onopen = onSocketOpen

    socket.onmessage = onSocketMessage

    socket.onclose = onSocketClose

    socket.onerror = onSocketError

    await startSocketHealthMonitor()
  } catch (e) {
    logger.error(e, 'Error connecting to websocket')
  }
}

const reconnect = async () => {
  logger.debug('WS::reconnect')
  socket.close()
  await stopSocketHealthMonitor()
}

/**
 * Socket health management
 *
 * 1. Every 15 seconds send a pong which is replied by ping from BE
 * 2. Close socket when connection is not healthy, os it can reconnect
 */

export const checkSocketHealth = async () => {
  if (!lastAliveCheck) return

  const socketIsLost = lastAliveCheck < Date.now() - SILENCE_TIME_LIMIT
  const socketIsOpen = socket.readyState === socket.OPEN

  const shouldReconnect = socketIsLost && socketIsOpen
  if (shouldReconnect) {
    await reconnect()
  }
}

const stopSocketHealthMonitor = async () => {
  await Browser.alarms.clear(WEBSOCKET_ALARM_ID)
}
const startSocketHealthMonitor = async () => {
  await stopSocketHealthMonitor()
  Browser.alarms.create(WEBSOCKET_ALARM_ID, {
    periodInMinutes: HEALTH_CHECK_TIMEOUT_IN_MINUTE,
  })
}

/** Listen for change in user and drop/create WS connection */
export const onWebsocketStateChangesListener: StateChangeListener = async (
  changes,
  areaName
) => {
  try {
    if (areaName === 'local' && UsersService.storageKey in changes) {
      const newValue = changes[UsersService.storageKey].newValue?.id
      const oldValue = changes[UsersService.storageKey].oldValue?.id

      if (oldValue !== newValue) {
        if (socket) {
          await reconnect()
        }
        await connectWebSocket()
      }
    }
  } catch (error) {
    logger.error(error, 'onWebsocketStateChangesListener')
  }
}
