import { type TimeEntryConstraints, type TimeEntryV9 } from '@toggl/track-types'

const TEConstraintsService = {
  check(
    constraints: TimeEntryConstraints | null,
    data?: Pick<
      TimeEntryV9,
      'description' | 'project_id' | 'tag_ids' | 'task_id'
    >
  ) {
    if (
      !data ||
      !constraints ||
      constraints.time_entry_constraints_enabled === false
    ) {
      return []
    }

    const missing: Array<string> = []

    if (constraints.description_present && !data.description.length) {
      missing.push('description')
    }

    if (constraints.project_present && !data.project_id) {
      missing.push('project')
    }

    if (constraints.tag_present && !data.tag_ids?.length) {
      missing.push('tag')
    }

    if (constraints.task_present && !data.task_id) {
      missing.push('task')
    }

    return missing
  },
}

export { TEConstraintsService }
