import { type DateTimeDuration } from '@internationalized/date'
import * as timeEntryService from '@src/background/_internal/timeEntry'
import { logger } from '@src/lib/errorReport'
import intl from '@src/lib/intl'
import { CurrentTEService } from '@src/v4/services/CurrentTEService'
import { NotificationService } from '@src/v4/services/NotificationService'
import { UsersService } from '@src/v4/services/UsersService'
import { DateTime } from 'luxon'
import { defineMessages } from 'react-intl'
import Browser from 'webextension-polyfill'

import { type TimeEntryRequest, type TimeEntryV9 } from '@toggl/track-types'

import { Db } from './db'
import { stopRunningTimeEntry } from './timeEntries'

const messages = defineMessages({
  idleFor: {
    id: 'idle.notifications.idleFor',
    defaultMessage: `You have been idle for {time} while tracking "{description}"`,
  },
  discardIdleButton: {
    id: 'idle.notifications.discardIdleButton',
    defaultMessage: 'Stop timer and discard idle time',
  },
  continue: {
    id: 'idle.notifications.keepIdleButton',
    defaultMessage: 'Discard idle time and continue',
  },
  timeEntryConstraints: {
    id: 'idle.notifications.timeEntryConstraints',
    defaultMessage: `To stop the timer, add required {fields} field(s).`,
  },
  requiredField: {
    id: 'idle.notifications.requiredFields',
    defaultMessage:
      '{field, select, description {description} tag {tag} project {project} task {task} other {Unknown}}',
  },
})

type LastIdleTimeEntry = {
  since: string
  timeEntryId: TimeEntryV9['id']
  alertTriggered?: boolean
}

export const IDLE_NOTIFICATION_ID = 'idle-notification'

export async function idleListener(state: Browser.Idle.IdleState) {
  try {
    logger.debug('idleListener', state, DateTime.now().toISO())
    const isIdleEnabled = await Db.get('idleDetectionEnabled')
    const user = await UsersService.retrieve()
    if (!isIdleEnabled || !user) {
      return
    }

    const current = await CurrentTEService.retrieve()

    if (!current) {
      await Db.setLocal('lastIdleTimeEntry', null)
      return
    }

    const lastIdleTimeEntry = await Db.getLocal('lastIdleTimeEntry')

    if (
      (state === 'idle' || state === 'locked') &&
      (!lastIdleTimeEntry || lastIdleTimeEntry.alertTriggered)
    ) {
      logger.debug(
        'idleListener',
        state,
        DateTime.now().toISO(),
        'starting idle tracking'
      )
      await Db.setLocal('lastIdleTimeEntry', {
        since: DateTime.now().toISO(),
        timeEntryId: current.id,
        alertTriggered: false,
      })
    }

    if (state === 'active') {
      const lastIdleTimeEntry = await Db.getLocal('lastIdleTimeEntry')
      if (!lastIdleTimeEntry) {
        return
      }

      logger.debug(
        'idleListener',
        DateTime.now().toISO(),
        'notificationTriggered'
      )
      await Db.setLocal('lastIdleTimeEntry', {
        since: lastIdleTimeEntry.since,
        timeEntryId: lastIdleTimeEntry.timeEntryId,
        alertTriggered: true,
      })
      await triggerIdleNotification(current, lastIdleTimeEntry)
    }
  } catch (e) {
    logger.error(e)
  }
}

async function triggerIdleNotification(
  runningTimeEntry: TimeEntryV9,
  lastIdleTimeEntry: LastIdleTimeEntry
) {
  const idleFor: DateTimeDuration = DateTime.now()
    .diff(DateTime.fromISO(lastIdleTimeEntry.since), ['minutes', 'seconds'])
    .toObject()
  const minutes = Math.floor(idleFor.minutes || 0)
  const seconds = Math.ceil(idleFor.seconds || 0)
  await Browser.notifications.create(IDLE_NOTIFICATION_ID, {
    type: 'basic',
    iconUrl: 'images/icon-128.png',
    title: 'Toggl Track',
    message: intl.formatMessage(messages.idleFor, {
      time:
        minutes > 0
          ? `${minutes} minutes and ${seconds} seconds`
          : `${seconds}s`,
      description: runningTimeEntry.description || 'No Description',
    }),
    priority: 2,
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore: webextension-polyfill is outdated
    buttons: runningTimeEntry
      ? [
          {
            title: intl.formatMessage(messages.discardIdleButton),
          },
          {
            title: intl.formatMessage(messages.continue),
          },
        ]
      : [],
  })
}

export async function idleNotificationClickHandler(
  _: string,
  buttonIndex: number
) {
  try {
    await Browser.notifications.clear(IDLE_NOTIFICATION_ID)
    const shouldContinue = buttonIndex === 1
    const runningTimeEntry = await CurrentTEService.retrieve()

    const lastIdleTimeEntry = await Db.getLocal('lastIdleTimeEntry')

    if (runningTimeEntry && lastIdleTimeEntry) {
      await stopRunningTimeEntry(runningTimeEntry, lastIdleTimeEntry.since)
      if (shouldContinue) {
        const start = new Date()
        const entry = {
          at: start.toISOString(),
          billable: runningTimeEntry.billable,
          description: runningTimeEntry.description,
          duration: -parseInt((start.getTime() / 1000).toFixed(0), 10),
          start: start.toISOString(),
          tags: runningTimeEntry.tags ?? [],
          tag_ids: runningTimeEntry.tag_ids ?? [],
          task_id: runningTimeEntry.task_id,
          project_id: runningTimeEntry.project_id,
          workspace_id: runningTimeEntry.workspace_id,
        } as unknown as TimeEntryRequest

        await timeEntryService.start(entry, { type: 'contentScript' })
      }
    }
  } catch (error) {
    logger.error(error, 'Error processing idleNotificationClickHandler')
    await NotificationService.create('Something went wrong', error.message)
  }
}

export async function configureIdleDetector() {
  logger.debug('Idle::configureIdleDetector')
  const idleInterval = await Db.get('idleInterval')
  Browser.idle.setDetectionInterval(Number(idleInterval))
}

Browser.storage.onChanged.addListener(async (changes, areaName) => {
  if (areaName === 'sync' && 'idleInterval' in changes) {
    await configureIdleDetector()
  }
})
