import { DateTime } from 'luxon'

export const getRemainingSessionTime = (
  startedAt: number,
  duration: number
) => {
  const startedAtInLuxon = DateTime.fromMillis(startedAt)
  const elapsed = DateTime.now().diff(startedAtInLuxon, 'seconds').seconds
  const result = duration - elapsed

  // Avoid NaN or Infinity values, the alarm will not be set.
  if (Number.isFinite(result)) {
    return result
  }

  return duration
}
