import * as datefns from 'date-fns'
import { chain, sortBy } from 'lodash'
import { DateTime } from 'luxon'
import * as R from 'ramda'

import { secToHhmmImproved } from '@toggl/time-format-utils'
import type {
  TempTimeEntry,
  TimeEntry,
  TimeEntryV9,
  Workspace,
} from '@toggl/track-types'

const MAX_DURATION = 3596400 // 999 hours in seconds
const HHMMSS_DURATION_REGEX =
  /^\s*(\d+)?[ :;_/-]?([\d,.]+)?:?([[ :;_/-]+[\d,.]+)?\s*$/
const HUMAN_DURATION_REGEX =
  /^\s*((\d+(?:\.|,)?\d*|\d*(?:\.|,)?\d+)\s*(((m|min|mins|minute|minutes)?)|((h|hr|hrs|hour|hours)?)|((s|sec|secs|second|seconds)?)|((d|ds|day|days)?)|((w|wk|wks|week|weeks)?))?\s*)+$/i
const LOAD_MORE_RANGE_IN_DAYS = 5
const VALID_TIME_FORMATS = (() => {
  const formats = R.xprod(
    R.xprod(R.xprod(['h', 'hh', 'H', 'HH', 'k'], ['m', 'mm', '']), [
      '',
      ' ',
      ',',
      ';',
      ':',
      '.',
    ]),
    ['', 'aaa', ' aaa']
  ).reduce((formats: string[], [[[hours, minutes], delimiter], period]) => {
    let format = ''

    if (minutes) {
      format += `${hours}${delimiter}${minutes}`
    } else {
      format += hours
    }

    if (!(hours.includes('H') || hours.includes('k'))) {
      format += period
    }

    formats.push(format)

    return formats
  }, [])

  return R.uniq(formats)
})()

/**
 * Returns a DateTime object give a string (ISO format) / Date
 *
 * @param {string | Date} date
 * @returns {DateTime}
 */
const getDateTime = (date: string | Date) =>
  typeof date === 'string' ? DateTime.fromISO(date) : DateTime.fromJSDate(date)

/**
 * Returns stop date of given time entry
 *
 * @param {object} timeEntry Time entry
 * @returns {string}
 */

export const getStopDate = (timeEntry: TimeEntry | TimeEntryV9): string =>
  timeEntry.stop ??
  getDateTime(timeEntry.start)
    .plus(getDuration(timeEntry) * 1000)
    .toISO()

export const getStartDateSeconds = (
  timeEntry: TimeEntryV9 | TimeEntry
): number => getDateTime(timeEntry.start).toUnixInteger()

/**
 * Returns stop date of given time entry as a unix timestamp (seconds)
 *
 * @param {object} timeEntry Time entry
 * @returns {number}
 */
export const getStopDateSeconds = (
  timeEntry: TimeEntry | TimeEntryV9
): number => DateTime.fromISO(getStopDate(timeEntry)).toUnixInteger()

/**
 * Returns the duration of a time entry. Also running entries.
 *
 * @param {object} timeEntry Time entry
 * @returns {number} Duration in seconds
 */
export const getDuration = (timeEntry: TimeEntry | TimeEntryV9): number => {
  if (timeEntry.duration >= 0) {
    return timeEntry.duration
  }

  return calculateDuration(
    timeEntry.start,
    timeEntry.stop || DateTime.now().toISO()
  )
}

/**
 * Calculates duration from start and stop dates. Will work for running time
 * entries (without stop date), that should have a negative duration.
 *
 * For running time entries, duration is always rounded down to the second to
 * avoid setting stop time in the future (e.g. 1337ms becomes 1s).
 *
 * @param {string} start Time
 * @param {string} stop Time (optional)
 * @returns {number} Duration in seconds
 */
export const calculateDuration = (start: string, stop?: string): number => {
  const startValue = DateTime.fromISO(start).toSeconds()
  const stopValue = stop ? DateTime.fromISO(stop).toSeconds() : 0
  return Math.floor(stopValue - startValue)
}

/**
 * Calculates stop date from start date and duration. Will return null if
 * duration is negative, because that indicates a running time entry (without a
 * stop date).
 *
 * @param {string} start Time
 * @param {number} duration In seconds
 * @returns {string} String
 */
export const calculateStopDate = (
  start: string,
  duration: number
): string | null =>
  duration < 0
    ? null
    : DateTime.fromISO(start, { setZone: true })
        .plus(duration * 1000)
        .toISO({ includeOffset: true })

/**
 * Returns the earliest start date witin the given TEs
 *
 * @param {Object | Array} timeEntries Time entries
 * @returns {string}
 */
export const getGroupStartDate = (
  timeEntries: ReadonlyArray<TimeEntryV9>
): string =>
  timeEntries
    .map(({ start }) => start)
    .sort()
    .shift()!

/**
 * Returns the latest stop date witin the given TEs
 *
 * @param {Object | Array} timeEntries Time entries
 * @returns {string}
 */
export const getGroupStopDate = (
  timeEntries: ReadonlyArray<TimeEntryV9>
): string =>
  timeEntries
    .map(({ stop }) => stop ?? '')
    .sort()
    .pop()!

/**
 * Returns total duration in seconds of given TEs
 *
 * @param {Object | Array} timeEntries Time entries to sum
 * @returns {number} Total duration in seconds
 */
export const getGroupDuration = <T extends TempTimeEntry | TimeEntryV9>(
  timeEntries: ReadonlyArray<T>
): number =>
  timeEntries.reduce((sum: number, te: T) => {
    if (!te.duration) return sum

    const duration = getDuration(te)
    sum += duration
    return sum
  }, 0)

/**
 * Returns whether given duration is a valid duration for the Timer components
 *
 * @param {string} duration Duration
 * @returns {Boolean}
 */
export const isValidDuration = (duration: string): boolean =>
  isValidTimeDuration(duration) || isValidHumanDuration(duration)

/**
 * Returns whether given duration is a valid time duration (variants of hh:mm:ss)
 *
 * @param {string} duration Duration
 * @returns {Boolean}
 */
export const isValidTimeDuration = (duration: string): boolean =>
  HHMMSS_DURATION_REGEX.test(duration)

/**
 * Returns whether given duration is a valid human duration (variants of 1h30m,
 * 15m30secs, etc)
 *
 * @param {string} duration Duration
 * @returns {Boolean}
 */
export const isValidHumanDuration = (duration: string): boolean =>
  HUMAN_DURATION_REGEX.test(duration)

/**
 * Returns duration limited by the Max duration value
 *
 * @param {number} duration In seconds
 * @returns {number} Duration
 */
export const forceMaxDuration = (duration: number): number =>
  duration > MAX_DURATION ? MAX_DURATION : duration

/**
 * Returns moment duration object Accepts either 'human' durations (e.g.
 * 1h30minutes) or standard formats (e.g. 10:30:00).
 *
 * @param {string} str Duration string to parse
 * @returns {number | null} Duration in seconds
 */
export const parseDuration = (str: string): number => {
  const duration = isValidHumanDuration(str)
    ? parseHumanDuration(str)
    : parseTimeDuration(str)
  return duration ? forceMaxDuration(duration) : 0
}

/**
 * Returns duration in seconds of given string Accepts strings like:
 *
 * - 2 (2 min)
 * - 2.5 (2.5 min)
 * - 2:5 (2 min, 5 sec)
 * - 2:5:5 (2h, 5min, 5 sec)
 * - 2;5;5 (2h, 5min, 5 sec)
 * - 2::5 (2h, 0min, 5 sec)
 * - 2.5:5:5 (2h, 35min, 5 sec)
 * - 2,5,5 (2h, 30min, 30 sec)
 * - 2:65:5 (3h, 5min, 5 sec)
 *
 * @param {string} str Duration
 * @returns {number} Duration in seconds, always positive
 */
export const parseTimeDuration = (str: string): number => {
  str = str.replace(/[,.]+/g, '.').replace(/[ :;_/-]+/g, ':') // 2,5;5 => 2.5:5
  let hms = str.split(':').map((part) => parseFloat(part ?? '0'))

  if (hms.length === 1) {
    if (hms[0] > 99) hms = [Math.floor(hms[0] / 100), hms[0] % 100, 0]
    else hms = [0, hms[0], 0]
  }

  if (hms.length >= 2) hms = [hms[0], hms[1], hms[2] ?? 0]

  return Math.floor(
    (hms[0] || 0) * 60 * 60 + (hms[1] || 0) * 60 + (hms[2] || 0)
  )
}

/**
 * Returns seconds of the given duration. Returns null if invalid duration
 * format. Formats:
 *
 * - `43:22`, `8:15`, `7:2`, `:35`, `62:` -> hh:mm
 * - `3,5`, `74.56`, `,37`, `62.` -> hours
 * - `2`, `62`, 235` -> minutes
 * - `23 unit`, `57.3 unit` -> unit
 *
 * @param {string} inputDuration Input to parse
 * @returns {number | null}
 */
export const parseHumanDuration = (inputDuration: string): number | null => {
  let input = inputDuration
  let match = HHMMSS_DURATION_REGEX.exec(input)
  if (match) {
    return parseTimeDuration(input)
  }

  let accumulated = 0
  let value
  input = input.replace(/[,.]+/g, '.')
  match = HUMAN_DURATION_REGEX.exec(input)

  // Go through regex matches and accumulate a duration
  while (match) {
    value = +match[2]

    // Parse the value according to the group
    // If there was no group defined then parse the values as minutes
    // If `,` or `.` is used then parse as hours
    const hasUnitMinutes = match[5] != null
    const hasUnitHours = match[7] != null
    const hasUnitSeconds = match[9] != null
    const hasUnitDays = match[11] != null
    const hasUnitWeeks = match[13] != null
    const isDecimalNoUnit = /^\d*\.\d*$/.exec(match[1])

    if (hasUnitHours) {
      accumulated += value * 60 * 60
    } else if (hasUnitSeconds) {
      accumulated += value
    } else if (hasUnitMinutes) {
      accumulated += value * 60
    } else if (hasUnitDays) {
      accumulated += value * 24 * 60 * 60
    } else if (hasUnitWeeks) {
      accumulated += value * 7 * 24 * 60 * 60
    } else {
      // Default to minutes unit
      accumulated += value * 60
    }

    // We could just re-run `exec` on the string and it should work, but we'd
    // have to take the beginning of the line constraint and mess up validation.
    input = input.replace(match[1], '')
    match = HUMAN_DURATION_REGEX.exec(input)
    if (match && input) {
      if (isDecimalNoUnit) return null
    } else {
      return accumulated
    }
  }

  return accumulated
}

/**
 * Formats given string date to Today, Yesterday, or a narrow date format
 *
 * @param {string} date
 * @returns {string}
 */
export const formatDay = (date: string): string => {
  const d = new Date(date)

  if (datefns.isToday(d)) {
    return 'Today'
  }

  if (datefns.isYesterday(d)) {
    return 'Yesterday'
  }

  return datefns.format(d, 'EEE, dd MMM')
}

export const formatDuration = (
  duration: number,
  options?: { html: boolean }
): string => secToHhmmImproved(duration, options)

/**
 * Drops anything after the day period (AM/PM), if anything is present.
 *
 * @param {string} str The input string
 * @returns {string} The input string with anything after the period dropped
 */

const dropAfterPeriod = (str: string): string =>
  str.replace(/([ap]m?) .+/gi, '$1')

/**
 * Returns whether given time string is valid
 *
 * @param {string} time The input string
 * @returns {Boolean}
 * @see https://date-fns.org/v2.28.0/docs/parse
 */

export const isValidTime = (time: string): boolean => {
  time = dropAfterPeriod(time)

  return VALID_TIME_FORMATS.some(
    (format) => !isNaN(+datefns.parse(time, format, new Date()))
  )
}

/**
 * Parse time input into valid Date objects. Returns an object representing the
 * parsed time in 24h format.
 *
 * @param {string} str Input string
 * @param {Boolean} use24hClock Indicates whether prevDate should be used to
 *   determine if the time should be am or pm if it appears to be earlier than
 *   12pm and there is no info about whether it is actually am or pm
 * @param {string} prevDate If no am/pm specified in string, compare both to
 *   this date and use whatever is closest.
 * @returns {object} {hours: ..., minutes: ...}
 */
export const parseTime = (
  str: string,
  use24hClock: boolean,
  prevDate: null | string = null
): { hours: number; minutes: number } => {
  str = dropAfterPeriod(str)
    .toLowerCase()
    .replace(/[,.:;]+/, ':')
    .trim()

  /**
   * Parse hours and minutes from the time string and return the hours minute
   * components as elements of len-2 array.
   *
   * @param {string} str The input string such as '100', '1:00', '1200', '12:00'
   * @returns {array | undefined} Hh, mm
   */
  const getHoursMinutes = (str: string) => {
    if (str.indexOf(':') > -1) {
      return str.split(':').map((part) => parseInt(part, 10) || 0)
    } else {
      str = str.replace(/[^0-9]/g, '')
      if (str.length <= 2) {
        return [parseInt(str, 10), 0] // h and hh
      } else if (str.length <= 3) {
        return [parseInt(str[0], 10), parseInt(str.substr(1), 10)] // hmm
      } else {
        return [parseInt(str.substr(0, 2), 10), parseInt(str.substr(2), 10)] // hhmm and hhmmmmmm..
      }
    }
  }

  /**
   * Take an input array of [hh, mm] and add hours to `hh` as necessary if `mm` exceeds 59
   *
   * @param {array} hm [hh, mm]
   * @returns {array | undefined} Hh, mm
   */
  const overflowMinutes = (hm: number[] | [number, number]) => {
    hm = hm.slice() as [number, number]
    hm[0] += Math.floor(hm[1] / 60)
    hm[1] = hm[1] % 60
    return hm
  }

  let hm = getHoursMinutes(str)
  hm[0] = hm[0] % 24 // Lose extra days
  hm = overflowMinutes(hm)

  // If we're in 24h mode or the user inputs more than 12 hours, there's no need to add any more magic
  if (use24hClock || hm[0] >= 13) {
    return { hours: hm[0], minutes: hm[1] }
  }

  const isPM = _isPm(str, hm, use24hClock, prevDate)

  if (hm[0] === 12) hm[0] = 0 // twelvehouranians say 12:00 when they mean 0:00. We need to normalize that

  if (isPM === true) {
    hm[0] += 12
  }

  return { hours: hm[0], minutes: hm[1] }
}

/**
 * Return true if the time should be PM. The time is PM if the string contains
 * 'p' or 'pm' explicitly or, if no am/pm found in string, it should be PM if
 * that time is closer to the time in prevDate
 *
 * @param {string} str The time string like '11:22a', assumed to be in 12h format
 * @returns {boolean} True if PM
 */
const _isPm = (
  str: string,
  hm: number[],
  use24hClock: boolean,
  prevDate: null | string
) => {
  let isPM: boolean | null = null // null represents "we don't know"
  const ampmMatch = str.toLowerCase().match(/(a|p)m?$/)

  if (ampmMatch) {
    str = str.substr(0, str.length - ampmMatch[0].length)
    isPM = ampmMatch[0] === 'p' || ampmMatch[0] === 'pm'
  } else if (!use24hClock && prevDate) {
    // No am/pm found in string but we need *something* => take the closest value to prevDate
    if (hm[0] === 12) hm = [0, hm[1]] // twelvehouranians say 12:00 when they mean 0:00. We need to normalize that
    const dt = DateTime.now().set({ hour: hm[0], minute: hm[1] })

    const prevTimeInPrevDay = DateTime.now()
      .minus({ days: 1 })
      .set({
        hour: DateTime.fromISO(prevDate).hour,
        minute: DateTime.fromISO(prevDate).minute,
      })

    const prevTimeInCurrentDay = DateTime.now().set({
      hour: DateTime.fromISO(prevDate).hour,
      minute: DateTime.fromISO(prevDate).minute,
    })

    const prevTimeInNextDay = DateTime.now()
      .plus({ days: 1 })
      .set({
        hour: DateTime.fromISO(prevDate).hour,
        minute: DateTime.fromISO(prevDate).minute,
      })

    const diffPrevDay = Math.abs(dt.toMillis() - prevTimeInPrevDay.toMillis())
    const diffCurrentDayAM = Math.abs(
      dt.toMillis() - prevTimeInCurrentDay.toMillis()
    )
    const diffCurrentDayPM = Math.abs(
      dt.toMillis() + 12 * 60 * 60 * 1000 - prevTimeInCurrentDay.toMillis()
    )
    const diffNextDay = Math.abs(
      dt.toMillis() + 12 * 60 * 60 * 1000 - prevTimeInNextDay.toMillis()
    )
    const smallest = Math.min(
      diffPrevDay,
      diffCurrentDayAM,
      diffCurrentDayPM,
      diffNextDay
    )

    if (smallest === diffPrevDay) isPM = false
    else if (smallest === diffCurrentDayAM) isPM = false
    else if (smallest === diffCurrentDayPM) isPM = true
    else if (smallest === diffNextDay) isPM = true
  }

  return isPM
}

/**
 * Checks if the time entry is running
 *
 * @param {object} timeEntry A time entry object
 * @returns {Boolean}
 */
export const isTimeEntryRunning = (
  timeEntry: TimeEntryV9 | TempTimeEntry
): boolean => timeEntry.duration < 0

/**
 * Returns a new entry containing only the regular time entry attributes,
 * matching the format used by the time entries API.
 *
 * In detailed reports this is used to prevent leakage of report API attributes
 * to the timer page.
 *
 * @param {object} timeEntry A time entry object
 * @returns {object} The newtime entry
 */
export const pickTimeEntryAttributes = (
  timeEntry: TimeEntryV9
): Partial<TimeEntryV9> =>
  R.pick(
    [
      'id',
      'wid',
      'pid',
      'tid',
      'billable',
      'start',
      'stop',
      'duration',
      'description',
      'tags',
      'duronly',
      'at',
      'uid',
    ],
    timeEntry
  )

/**
 * Returns true if two entries should be grouped when presented, if grouped mode
 * is active.
 *
 * @param {object} timeEntryA A time entry
 * @param {object} timeEntryA Another time entry @ @return {boolean} true if should group
 */
export const shouldGroupTimeEntries = (
  timeEntryA: TimeEntryV9,
  timeEntryB: TimeEntryV9
): boolean => {
  if (!timeEntryA || !timeEntryB) return false
  if (
    (timeEntryA.workspace_id || timeEntryB.workspace_id) &&
    timeEntryA.workspace_id !== timeEntryB.workspace_id
  ) {
    return false
  }
  if (
    (timeEntryA.task_id || timeEntryB.task_id) &&
    timeEntryA.task_id !== timeEntryB.task_id
  ) {
    return false
  }
  if (
    (timeEntryA.project_id || timeEntryB.project_id) &&
    timeEntryA.project_id !== timeEntryB.project_id
  ) {
    return false
  }
  if (timeEntryA.billable !== timeEntryB.billable) return false
  if (R.difference(timeEntryA.tags ?? [], timeEntryB.tags ?? []).length)
    return false
  return (timeEntryA.description || '') === (timeEntryB.description || '')
}

export const getLoadMoreRangeInDays = (): number => LOAD_MORE_RANGE_IN_DAYS

const stripMilliseconds = (date: string) => {
  const dt = DateTime.fromISO(date)

  if (Number.isNaN(dt.toMillis())) {
    return date
  }

  return dt.set({ millisecond: 0 }).toUTC().toISO()
}

export const sanitizeTimes = (te: TimeEntryV9): TimeEntryV9 => ({
  ...te,
  start: stripMilliseconds(te.start),
  stop: te.stop ? stripMilliseconds(te.stop) : undefined,
})

// keeping this cache, but not sure if there's benefit
const unixTimeCache: Record<string, number> = {}
const cacheAndReturn = (key: string, fn: () => number) => {
  if (unixTimeCache[key]) return unixTimeCache[key]
  unixTimeCache[key] = fn()
  return unixTimeCache[key]
}
const unixStart = (te: TempTimeEntry | TimeEntryV9) =>
  cacheAndReturn(te.start, () => getStartDateSeconds(te))
const unixStop = (te: TempTimeEntry | TimeEntryV9) =>
  te.stop ? cacheAndReturn(te.stop, () => getStopDateSeconds(te)) : null

const isTEV9 = (te: TimeEntryV9 | TempTimeEntry): te is TimeEntryV9 =>
  'project_id' in te
export const groupSimilarTimeEntries = <T extends TempTimeEntry | TimeEntryV9>(
  timeEntries: ReadonlyArray<T>
): ReadonlyArray<T | ReadonlyArray<T>> =>
  chain(timeEntries)
    .groupBy((te) =>
      [
        te.description ?? '',
        ...(isTEV9(te)
          ? [te.project_id ?? '', te.task_id ?? '', te.workspace_id]
          : [te.pid ?? '', te.tid ?? '', te.wid]),
        (te.tag_ids ?? te.tags ?? []).join(','),
        te.billable === true,
      ].join(' - ')
    )
    .values()
    .map((group) => {
      if (group.length === 1) {
        return group[0]
      }

      return group
    })
    .sortBy((tes) => {
      const item = Array.isArray(tes)
        ? sortBy(tes, (te) => {
            if (te.start) return unixStart(te)
            return unixStop(te) ?? 0
          }).at(-1)!
        : tes

      return item.start ? unixStart(item) : unixStop(item) ?? 0
    })
    .reverse()
    .value()

export const getMinimumAllowedStartDate = (
  isAdminOfWorkspace: boolean,
  workspaceLockedAtDate?: string
) => {
  if (isAdminOfWorkspace) return null
  if (!workspaceLockedAtDate) return null
  // One day is added beacuse the workspace setting may be 2020-01-01, but we want to lock the
  // entire day in the UI (i.e. up until 2020-01-01T23:59:59 inclusive)
  return DateTime.fromISO(workspaceLockedAtDate)
    .startOf('day')
    .plus({ days: 1 })
    .toUTC()
    .toISO()
}

/**
 * Returns a new entry containing only regular time entry attributes that may be
 * changed by the user. This is used in conjunction with the timeEntryUpdate
 * action for optimistic updates in the calendar to avoid submitting fields that
 * would cause errors, such as a differerent ID being included in the change set
 * due to an optimistic create in progress.
 *
 * N.B. this is a stop-gap solution while much of time entry sync / utils are
 * untyped or unfinished.
 *
 * @param {object} timeEntry A time entry object
 * @returns {object} The new time entry
 */
export const pickTimeEntryAttributesForUpdate = (timeEntry: TimeEntryV9) => {
  return R.omit(['id', 'uid', 'at'], timeEntry)
}

/**
 * Returns whether this time entry is locked for the user
 *
 * @param {object} timeEntry The time entry to check
 * @param {object} workspacesById Map of workspaces by ID, typically from the store
 * @param {object} lockDatesByWs Map of workspace lock dates by ID, typically
 *   from the store
 * @returns {boolean} Whether the time entry should be locked for the user
 */
export const isTimeEntryLocked = (
  timeEntry: TimeEntryV9,
  workspacesById: Record<string, Workspace>,
  lockDatesByWs: Record<string, string>
) => {
  // @ts-expect-error: Remove non-v9 attribute fallback
  const workspaceId = timeEntry.workspace_id || timeEntry.wid
  const workspace = workspacesById[workspaceId]
  // TODO: We have logic repeated across packages. Maybe move webapp utils to this package.
  const isAdmin = workspace
    ? workspace.role
      ? workspace.role === 'admin'
      : workspace.admin
    : false
  const lockDate = lockDatesByWs[workspaceId]
  const minDate = getMinimumAllowedStartDate(isAdmin, lockDate)

  if (!minDate) return false

  return !(
    getDateTime(timeEntry.start).diff(DateTime.fromISO(minDate)).toMillis() >= 0
  ) // same or after
}

export const sanitizeV9Attributes = (timeEntry: TimeEntryV9) => {
  return R.omit(
    ['tag_ids', 'project_id', 'workspace_id', 'task_id', 'client_id'],
    timeEntry
  )
}

export const sanitizeDuration = (timeEntry: TimeEntryV9) => {
  if (!timeEntry || !timeEntry.duration) return timeEntry
  return {
    ...timeEntry,
    duration: Math.floor(timeEntry.duration),
  }
}

export const sanitizeTimeEntry = (timeEntry: TimeEntryV9) => {
  return sanitizeV9Attributes(sanitizeDuration(timeEntry))
}

export const sanitizeTimeEntries = (timeEntries: TimeEntryV9[]) => {
  if (Array.isArray(timeEntries)) {
    return timeEntries.map(sanitizeV9Attributes)
  }
}

export const teUpdateChecks = (timeEntryParam: TimeEntryV9) => {
  if (!timeEntryParam.id) throw new Error('Cannot update a TE without an id.')
  if (!timeEntryParam.stop && timeEntryParam.duration > 0) {
    throw new Error('A running TE must have negative duration.')
  }
  if (!!timeEntryParam.stop && timeEntryParam.duration < 0) {
    throw new Error('A stopped TE must have a positive duration.')
  }
  if (timeEntryParam.id < 0) {
    throw new Error('Cannot post an entry with a negative id to the API.')
  }

  const sanitizedTimeEntry = R.omit(
    ['unsynced', '_tempId', '__created', '__deleted'],
    sanitizeTimeEntry(timeEntryParam)
  )
  const timeEntryToUpdate = {
    ...sanitizedTimeEntry,
    // @ts-expect-error: Remove non-v9 attribute fallback
    pid: timeEntryParam?.pid ?? null,
    billable: timeEntryParam.billable ?? false, // Api wants a bool value, not null
    stop: getCorrectedStop(sanitizedTimeEntry),
  }

  return timeEntryToUpdate
}

/**
 * Returns a Time Entry with sanitized properties ready to send to the Toggl API.
 *
 * @param timeEntryParam Time Entry.
 * @param clientIdentifier Identifier for the app client making the request,
 *   e.g. 'Snowball' for the webapp.
 * @returns New Time Entry object.
 */
export const teCreateChecks = (
  timeEntryParam: TimeEntryV9,
  clientIdentifier: string
) => {
  const defaultValues = {
    created_with: clientIdentifier,
    pid: null,
    tid: null,
  }
  const sanitizedTimeEntry = R.omit(
    ['unsynced', '_tempId', '__created', '__deleted', 'id'],
    sanitizeTimeEntry(timeEntryParam)
  )
  const timeEntryToSave = {
    ...defaultValues,
    ...sanitizedTimeEntry,
    billable: timeEntryParam.billable ?? false, // Api wants a bool value, not null
    stop: getCorrectedStop(sanitizedTimeEntry),
  }

  return timeEntryToSave
}

const getCorrectedStop = (
  timeEntry: Pick<TimeEntryV9, 'duration' | 'start' | 'stop'>
) => {
  if (timeEntry.duration >= 0 && timeEntry.stop) {
    const stop = datefns.addSeconds(
      new Date(timeEntry.start),
      timeEntry.duration
    )

    if (datefns.differenceInSeconds(stop, new Date(timeEntry.stop)) !== 0) {
      return stop.toISOString()
    }
  }

  return timeEntry.stop
}
