import { logger } from '@src/lib/errorReport'
import { compareSemver } from '@src/v4/lib/semver'
import { V1PomodoroMigration } from '@src/v4/migrations/1_pomodoro_settings'
import Browser from 'webextension-polyfill'

const migrations = [V1PomodoroMigration]

export async function runMigration(
  details: Browser.Runtime.OnInstalledDetailsType
) {
  try {
    logger.info(
      `Updated from ${details.previousVersion} to ${process.env.VERSION}.`
    )

    const version = process.env.VERSION ?? Browser.runtime.getManifest().version
    const migrationVersion = await getMigrationLastRunVersion()
    // No need to migrate if migration already ran for it
    if (compareSemver(migrationVersion, version) >= 0) {
      return
    }

    for (const migration of migrations) {
      // Run migration if migration script's version is greater than or equal to current version
      if (compareSemver(migration.version, version) >= 0) {
        logger.info(`Running migration: ${migration.name}`)
        await migration.run()
      }
    }
    await setMigrationLastRunVersion(version)
  } catch (error) {
    logger.error(error, 'runMigration')
  }
}

async function getMigrationLastRunVersion() {
  const { migrationVersion } = await Browser.storage.sync.get(
    'migrationVersion'
  )
  return migrationVersion ?? '0.0.0'
}

async function setMigrationLastRunVersion(version: string) {
  await Browser.storage.sync.set({
    migrationVersion: version,
  })
}
