import * as timeEntriesApi from '@src/background/_internal/timeEntry'
import { TEConstraintsService } from '@src/v4/services/TEConstraintsService'
import { WorkspacesListService } from '@src/v4/services/WorkspacesListService'
import { DateTime } from 'luxon'
import { defineMessages } from 'react-intl'

import { type TimeEntryV9 } from '@toggl/track-types'

import intl from './intl'

const messages = defineMessages({
  timeEntryConstraints: {
    id: 'timeEntries.notifications.timeEntryConstraints',
    defaultMessage: `To stop the timer, add required {fields} field(s).`,
  },
  requiredField: {
    id: 'timeEntries.notifications.requiredFields',
    defaultMessage:
      '{field, select, description {description} tag {tag} project {project} task {task} other {Unknown}}',
  },
})

// TODO: This needs attention, also there's the constraint management and the notification.
export async function stopRunningTimeEntry(
  timeEntry: TimeEntryV9,
  stop: string | null,
  opts = { isManuallyStopped: true }
) {
  const stopTime = stop ? DateTime.fromISO(stop) : DateTime.now()
  const startTime = DateTime.fromISO(timeEntry.start)

  if (stopTime < startTime) {
    await timeEntriesApi.remove(timeEntry)
  }

  const wss = await WorkspacesListService.retrieve()

  let errors: string[] = []

  if (wss && timeEntry.workspace_id in wss) {
    errors = TEConstraintsService.check(
      wss[timeEntry.workspace_id].te_constraints ?? null,
      timeEntry
    )
  }

  if (errors.length === 0) {
    return timeEntriesApi.update(
      {
        ...timeEntry,
        tag_ids: timeEntry.tag_ids ?? [],
        stop: stopTime.toISO(),
        duration: Math.floor(stopTime.diff(startTime).as('seconds')),
      },
      {
        type: 'popup',
      },
      opts
    )
  } else {
    // TODO: This error will not be bubbled up to the mutation level.
    // We need to check how this fn is used and how to handle the error.
    throw new Error(
      intl.formatMessage(messages.timeEntryConstraints, {
        fields: intl.formatList(
          errors.map((field) =>
            intl.formatMessage(messages.requiredField, { field })
          )
        ),
      })
    )
  }
}
