import {
  IDLE_NOTIFICATION_ID,
  idleNotificationClickHandler,
} from '@src/lib/idle'
import {
  REMINDER_NOTIFICATION_ID,
  handleNotificationButtonClick,
} from '@src/lib/reminders'
import Browser from 'webextension-polyfill'

// a top level notification button handler to ensure we don't leak notification button handlers
// by forgetting to clear them

Browser.notifications.onButtonClicked.addListener(
  (notificationId, buttonIndex) => {
    if (notificationId === IDLE_NOTIFICATION_ID) {
      void idleNotificationClickHandler(notificationId, buttonIndex)
    } else if (notificationId === REMINDER_NOTIFICATION_ID) {
      void handleNotificationButtonClick()
    }
  }
)
