import { Db } from '@src/lib/db'
import { logger } from '@src/lib/errorReport'
import { isWebsocketActive } from '@src/lib/fetch-handler'
import {
  ensureSameWorkspace,
  setProject,
  updateDefaultProjects,
} from '@src/lib/projectUtils'
import { stopRunningTimeEntry } from '@src/lib/timeEntries'
import { config as onboardingConfig } from '@src/onboarding/config'
import { PomodoroService } from '@src/pomodoro/PomodoroService'
import { handleTimeEntryEndpointError } from '@src/v4/lib/handleTimeEntryEndpointError'
import { CurrentTEService } from '@src/v4/services/CurrentTEService'
import { type MessageFrom, type MutableTimeEntryV9 } from 'src/typdef'

import { type TimeEntryRequest, type TimeEntryV9 } from '@toggl/track-types'

import apiClient from './_api'

type MutableTimeEntryRequest = {
  -readonly [K in keyof Omit<
    TimeEntryRequest,
    'created_with'
  >]: TimeEntryRequest[K]
}

const create = async (
  entry: Omit<TimeEntryRequest, 'created_with'>,
  from?: MessageFrom
) => {
  try {
    const current = isWebsocketActive
      ? await CurrentTEService.retrieve()
      : await CurrentTEService.invoke()

    if (current) {
      await stopRunningTimeEntry(current, null)
    }

    const te = Object.assign({}, entry) as MutableTimeEntryRequest
    delete te.stop
    const { data } = await apiClient.workspaces.timeEntries.create({
      ...te,
      created_with: `TrackExtension/${process.env.VERSION}${
        from?.source ? `-${from.source}` : ''
      }`,
    })

    return data
  } catch (e) {
    logger.error(e)
    throw e
  }
}

export const start = async (entry: TimeEntryRequest, from: MessageFrom) => {
  try {
    let _entry = (await setProject(entry, from)) as unknown as TimeEntryRequest
    _entry = (await ensureSameWorkspace(_entry)) as unknown as TimeEntryRequest

    Db.set(onboardingConfig.storage, 'finished')

    const data = await create(_entry, from)

    // Start the pomodoro timer
    const settings = PomodoroService.getSettings()

    if (settings.isEnabled) {
      PomodoroService.start(new Date(data.start).getTime())
    }

    // Update storage with running TE.
    await CurrentTEService.store(data)

    return data
  } catch (error) {
    await handleTimeEntryEndpointError(error)
    throw error
  }
}

export const update = async (
  entry: TimeEntryV9,
  from: MessageFrom,
  opts = { isManuallyStopped: true }
) => {
  try {
    if (from.type === 'contentScript')
      updateDefaultProjects(from, entry.project_id)
    const _entry = await ensureSameWorkspace(entry)

    // TODO: Here there is not constraint management so the TINYBUTTON will not stop the TE.

    delete (_entry as MutableTimeEntryV9).stop
    const { data } = await apiClient.workspaces.timeEntries.update(_entry)

    const running = data.duration < 0 ? data : null
    await CurrentTEService.store(running)

    // Start the pomodoro timer
    const settings = PomodoroService.getSettings()

    // Reset the pomodoro timer if the entry is stopped.
    if (settings.isEnabled && opts.isManuallyStopped && running === null) {
      PomodoroService.reset()
    }

    return data
  } catch (e) {
    logger.error(e)
    await handleTimeEntryEndpointError(e, entry)
    throw e
  }
}

export const remove = async (timeEntry: TimeEntryV9) => {
  try {
    const current = await CurrentTEService.retrieve()

    await apiClient.workspaces.timeEntries.delete(timeEntry.workspace_id, [
      timeEntry.id,
    ])

    if (current?.id === timeEntry.id) {
      PomodoroService.reset()
    }
  } catch (e) {
    logger.error(e)
    await handleTimeEntryEndpointError(e, timeEntry)
    throw e
  }
}
