import { logger } from '@src/lib/errorReport'
import { FullFetchAlarm } from '@src/lib/full-fetch'
import { ClientsListService } from '@src/v4/services/ClientsListService'
import { CurrentTEService } from '@src/v4/services/CurrentTEService'
import { ProjectsListService } from '@src/v4/services/ProjectsListService'
import { TagsListService } from '@src/v4/services/TagsListService'
import { TasksListService } from '@src/v4/services/TasksListService'
import { UsersService } from '@src/v4/services/UsersService'
import { WorkspacesListService } from '@src/v4/services/WorkspacesListService'
import _ from 'lodash'
import Browser from 'webextension-polyfill'

import { RequestError } from '@toggl/track-api'

async function hasWorkspacesChanged() {
  const storedWorkspaceIds = Object.keys(
    (await WorkspacesListService.retrieve()) ?? {}
  )
  const workspaces = await WorkspacesListService.invoke(
    await WorkspacesListService.since()
  )
  const newWorkspaceIds = Object.keys(workspaces ?? {})
  return !_.isEqual(storedWorkspaceIds, newWorkspaceIds)
}

export async function doFetchUsingSince() {
  logger.debug('Lib::doFetchUsingSince')
  try {
    await UsersService.invoke({ store: true })

    // Changes in workspaces user is part of should trigger full fetch
    if (await hasWorkspacesChanged()) {
      await FullFetchAlarm.handler()
      return
    }

    await Promise.all([
      CurrentTEService.invoke({ store: true }),
      TagsListService.invoke(await TagsListService.since()),
      ClientsListService.invoke(await ClientsListService.since()),
    ])
    const projects = await ProjectsListService.invoke(
      await ProjectsListService.since(),
      // return api data instead of storage
      { returnApiData: true }
    )
    // Perform full task since if user is added or removed from project
    // We don't get tasks as part of since for this case
    await TasksListService.invoke(
      Object.keys(projects ?? {}).length === 0
        ? await TasksListService.since()
        : null
    )
  } catch (error) {
    if (error instanceof RequestError && error.response?.status !== 403) {
      Browser.alarms.create(FullFetchAlarm.retryAlarmName, {
        when: Date.now() + 5 * 60 * 1000,
      })
    }
    logger.error(error) // TODO: Does this needs to go to Sentry as well? @with-shrey @askides
  }
}
