import { Db } from '@src/lib/db'
import { logger } from '@src/lib/errorReport'
import intl from '@src/lib/intl'
import { extractHostname } from '@src/lib/utils'
import { NotificationService } from '@src/v4/services/NotificationService'
import { SelectedWorkspaceService } from '@src/v4/services/SelectedWorkspaceService'
import { DateTime } from 'luxon'
import { defineMessages } from 'react-intl'
import * as Browser from 'webextension-polyfill'

import { type TimeEntryRequest } from '@toggl/track-types'

import * as timeEntryBackgroundService from './_internal/timeEntry'

const messages = defineMessages({
  startTimerWithDescription: {
    id: 'contextMenu.startTimerWithDescription',
    defaultMessage: 'Start timer with description',
  },
  startTimer: {
    id: 'contextMenu.startTimer',
    defaultMessage: 'Start timer',
  },
  disableTinyButton: {
    id: 'contextMenu.disableTinyButton',
    defaultMessage: 'Disable tiny button on this page',
  },
  enableTinyButton: {
    id: 'contextMenu.enableTinyButton',
    defaultMessage: 'Re-enable tiny button on this page',
  },
})

const handleButtonClicked = async (
  info: Browser.Menus.OnClickData,
  tab: Browser.Tabs.Tab
) => {
  try {
    if (
      info.menuItemId === 'TrackExtension' ||
      info.menuItemId === 'TrackExtensionSelection'
    ) {
      await startTimer(info, tab)
    } else {
      await toggleTinyButton(info, tab)
    }
  } catch (error) {
    logger.error(error)
    await NotificationService.create('Something went wrong', error.message)
  }
}

const toggleTinyButton = async (
  info: Browser.Menus.OnClickData,
  tab: Browser.Tabs.Tab
) => {
  const tinyButtonBlocklist = await Db.get('tinyButtonBlocklist')

  const hostname = extractHostname(tab.url!)

  const isNewStateDisabled = !tinyButtonBlocklist.includes(hostname)
  !isNewStateDisabled
    ? await Db.set(
        'tinyButtonBlocklist',
        tinyButtonBlocklist.filter((item) => item !== hostname)
      )
    : await Db.set('tinyButtonBlocklist', [...tinyButtonBlocklist, hostname])
  await Browser.tabs.sendMessage(tab.id!, {
    type: isNewStateDisabled ? 'hide-tiny-button' : 'show-tiny-button',
  })

  await Browser.contextMenus.update('TrackExtensionDisableTinyButton', {
    title: isNewStateDisabled
      ? intl.formatMessage(messages.enableTinyButton)
      : intl.formatMessage(messages.disableTinyButton),
  })
}

const startTimer = async (info: Browser.Menus.OnClickData, tab) => {
  const start = DateTime.now()
  const description = info?.selectionText || tab.title

  const entry = {
    at: start.toISO(),
    billable: false,
    description,
    duration: -parseInt((start.toMillis() / 1000).toFixed(0), 10),
    start: start.toISO(),
    tags: [],
    tag_ids: [],
    workspace_id: (await SelectedWorkspaceService.retrieve()) as number,
  } as unknown as TimeEntryRequest

  await timeEntryBackgroundService.start(entry, { type: 'contentScript' })
}

export const configure = async (tab: Browser.Tabs.Tab) => {
  const showButton = await Db.get('showRightClickButton')
  const tinyButtonBlocklist = await Db.get('tinyButtonBlocklist')
  const isTinyButtonDisabled = (tinyButtonBlocklist ?? []).includes(
    extractHostname(tab.url || '')
  )

  try {
    await Browser.contextMenus.remove('TrackExtension')
  } catch {
    // ignore
  }

  try {
    await Browser.contextMenus.remove('TrackExtensionSelection')
  } catch {
    // ignore
  }

  try {
    await Browser.contextMenus.remove('TrackExtensionDisableTinyButton')
  } catch {
    // ignore
  }

  if (showButton) {
    Browser.contextMenus.create({
      id: 'TrackExtension',
      title: intl.formatMessage(messages.startTimer),
      contexts: ['page'],
    })

    Browser.contextMenus.create({
      id: 'TrackExtensionSelection',
      title: intl.formatMessage(messages.startTimerWithDescription) + " '%s'",
      contexts: ['selection'],
    })
  }
  const tinyButtonEnabled = await Db.get('enableTinyButton')
  if (tinyButtonEnabled) {
    Browser.contextMenus.create({
      id: 'TrackExtensionDisableTinyButton',
      title: isTinyButtonDisabled
        ? intl.formatMessage(messages.enableTinyButton)
        : intl.formatMessage(messages.disableTinyButton),
      contexts: ['page'],
    })
  }

  Browser.contextMenus.onClicked.removeListener(handleButtonClicked)
  Browser.contextMenus.onClicked.addListener(handleButtonClicked)
}
