import { logger } from '@src/lib/errorReport'
import { CurrentTEService } from '@src/v4/services/CurrentTEService'
import { DateTime, Interval } from 'luxon'
import { defineMessages } from 'react-intl'
import Browser from 'webextension-polyfill'

import { Db, type StateChangeListener } from './db'
import intl from './intl'

export const TRACKING_REMINDER_ALARM_ID = 'time-tracking-reminder'
export const REMINDER_NOTIFICATION_ID = 'reminder-notification'

const messages = defineMessages({
  notification: {
    id: 'notification.rememberTracking',
    defaultMessage: "Don't forget to track your time.",
  },
  disableReminder: {
    id: 'notification.disableReminder',
    defaultMessage: 'Disable reminder',
  },
})

export async function configurePeriodicReminder() {
  const enabled = await Db.get('remindersCheckEnabled')

  await Browser.alarms.clear(TRACKING_REMINDER_ALARM_ID)

  if (!enabled) return

  await createPeriodicReminder()
}

async function createPeriodicReminder() {
  const periodicity = await Db.get('remindersInterval')

  Browser.alarms.create(TRACKING_REMINDER_ALARM_ID, {
    periodInMinutes: periodicity,
  })
}

export async function handleReminderAlarm() {
  try {
    logger.debug('Reminders::handleReminderAlarm')
    const [from, to] = await Db.get('remindersFromTo')
    const currentTE = await CurrentTEService.retrieve()

    const interval = Interval.fromDateTimes(
      DateTime.fromISO(from),
      DateTime.fromISO(to)
    )

    if (interval.contains(DateTime.now()) && !currentTE) {
      void Browser.notifications.create(REMINDER_NOTIFICATION_ID, {
        type: 'basic',
        iconUrl: 'images/icon-128.png',
        title: 'Toggl Track',
        priority: 2,
        message: intl.formatMessage(messages.notification),
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        buttons: [
          {
            title: intl.formatMessage(messages.disableReminder),
          },
        ],
      })
    }
  } catch (e) {
    logger.error(e)
  }
}

export async function clearPeriodicReminder() {
  await Browser.alarms.clear(TRACKING_REMINDER_ALARM_ID)
}

export async function handleNotificationButtonClick() {
  await Browser.notifications.clear(REMINDER_NOTIFICATION_ID)
  await Db.set('remindersCheckEnabled', false)
}

// Start instantly when setting is enabled
export const onRemindersStateChangeListener: StateChangeListener = (
  changes,
  area
) => {
  try {
    if (
      area === 'sync' &&
      (changes.remindersCheckEnabled || changes.remindersInterval)
    ) {
      logger.debug('Reminders::onRemindersStateChangeListener', new Date())
      void configurePeriodicReminder()
    }

    if (area === 'local' && changes[CurrentTEService.storageKey]) {
      if (changes[CurrentTEService.storageKey].newValue) {
        logger.debug(
          'Reminders::onRemindersStateChangeListener',
          'alarm cleared on storage change'
        )

        void clearPeriodicReminder()
      } else {
        logger.debug(
          'Reminders::onRemindersStateChangeListener',
          'alarm configuring on storage change',
          new Date()
        )
        void configurePeriodicReminder()
      }
    }
  } catch (error) {
    logger.error(error, 'onRemindersStateChangeListener')
  }
}
