import { LOCAL_STATE } from '@src/lib/constants'
import { Db } from '@src/lib/db'
import { useBrowserStorageLocal } from '@src/v4/lib/use-browser-storage'
import { ClientsListService } from '@src/v4/services/ClientsListService'
import { CurrentTEService } from '@src/v4/services/CurrentTEService'
import { OrgFeaturesService } from '@src/v4/services/OrgFeaturesService'
import { ProjectsListService } from '@src/v4/services/ProjectsListService'
import { SelectedWorkspaceService } from '@src/v4/services/SelectedWorkspaceService'
import { TagsListService } from '@src/v4/services/TagsListService'
import { TasksListService } from '@src/v4/services/TasksListService'
import { UsersService } from '@src/v4/services/UsersService'
import { WorkspacesListService } from '@src/v4/services/WorkspacesListService'
import { WSPreferencesService } from '@src/v4/services/WSPreferencesService'
import Browser from 'webextension-polyfill'

import { RequestError } from '@toggl/track-api'

import { logger } from './errorReport'

export const FullFetchAlarm = {
  name: 'FULL_FETCH_ALARM',
  retryAlarmName: 'FULL_FETCH_RETRY',
  statusKey: 'fullFetchStatus' as const,

  handler: async () => {
    logger.debug('Alarm::FullFetchAlarm.handler')
    const status = await FullFetchAlarm.updateStatus({
      loading: true,
      error: null,
      lastRunAt: new Date().toISOString(),
    })
    // Run a full fetch if the user is logged in.
    try {
      // No user? Throw an error.
      await UsersService.invoke({ store: true })

      await Promise.all([
        CurrentTEService.invoke({ store: true }),
        WorkspacesListService.invoke(null),
        TagsListService.invoke(null),
        TasksListService.invoke(null),
        ClientsListService.invoke(null),
        ProjectsListService.invoke(null),
        OrgFeaturesService.invoke(),

        // This one needs to be invoked with a workspace ID
        WSPreferencesService.invoke(
          (await SelectedWorkspaceService.retrieve()) ?? 0,
          {
            store: true,
          }
        ),
      ])
      await FullFetchAlarm.updateStatus({
        loading: false,
        lastSuccessfulAt: new Date().toISOString(),
        retryCount: 0,
      })
      await Browser.alarms.clear(FullFetchAlarm.retryAlarmName)
    } catch (error) {
      const newStatus = await FullFetchAlarm.updateStatus({
        loading: false,
        error: error.message,
        retryCount: (status.retryCount ?? 0) + 1,
      })

      if (error instanceof RequestError && error.response?.status !== 403) {
        // Exponential backoff with a max of 60 minutes
        const backOffMinutes = Math.min(Math.pow(2, newStatus.retryCount), 30)
        Browser.alarms.create(FullFetchAlarm.retryAlarmName, {
          when: Date.now() + 60 * 1000 * backOffMinutes,
        })
      }
      logger.error(error) // TODO: Does this needs to go to Sentry as well? @with-shrey @askides
    }
  },

  updateStatus: async (
    upsertData: Partial<typeof LOCAL_STATE['fullFetchStatus']>
  ) => {
    const lastFullFetch = await Db.getLocal(FullFetchAlarm.statusKey)
    const newState = {
      ...lastFullFetch,
      ...upsertData,
    }
    await Db.setLocal(FullFetchAlarm.statusKey, newState)
    return newState
  },

  configure() {
    Browser.alarms.create(FullFetchAlarm.name, {
      periodInMinutes: 2 * 60, // 2 hours
    })
  },
}

export const useFullFetchStatus = () => {
  return useBrowserStorageLocal<typeof LOCAL_STATE['fullFetchStatus']>(
    FullFetchAlarm.statusKey,
    LOCAL_STATE[FullFetchAlarm.statusKey]
  )
}
