import Browser from 'webextension-polyfill'

import { defaultSettings, initialState } from './config'

export type PomodoroState = {
  isRunning: number | null
  isBreak: boolean
  isLargeBreak: boolean
  session: number
  duration: number
  computed: {
    isIdle: boolean
    isIdleBreak: boolean
    isRunningBreak: boolean
    isRunningFocus: boolean
  }
}

export type PomodoroSettings = {
  isEnabled: boolean
  isTickingEnabled: boolean
  focusDuration: number
  breakDuration: number
  longBreakDuration: number
  sessions: number
  autoStartBreak: boolean
  autoStartFocus: boolean
  isAlarmEnabled: boolean
  alarmVolumePercent: number
}

type EventCallback = (state: PomodoroState) => void

type PomodoroEvents =
  | 'clock:init'
  | 'clock:start'
  | 'clock:reset'
  | 'state:change'

class Pomodoro {
  private state: PomodoroState
  private settings: PomodoroSettings
  private events: { [key: string]: EventCallback[] } = {}

  constructor() {
    this.state = initialState
  }

  async init({ force = false } = {}) {
    await this.fillSettings()

    if (force) {
      this.state = {
        ...initialState,
        duration: this.settings.focusDuration,
      }
    } else {
      // Check if the state is stored and restore it.
      const stateFromStorage = await this.getStoredState()

      if (stateFromStorage) {
        this.state = stateFromStorage
      } else {
        this.state = {
          ...initialState,
          duration: this.settings.focusDuration,
        }
      }
    }

    this.emit('clock:init', this.getState())
    this.emit('state:change', this.getState())
  }

  getStoredSettings() {
    return Browser.storage.sync.get('Settings.Pomodoro').then((data) => {
      return data['Settings.Pomodoro'] as PomodoroSettings
    }) as Promise<PomodoroSettings>
  }

  setStoredSettings(settings: Partial<PomodoroSettings>) {
    return Browser.storage.sync.set({ 'Settings.Pomodoro': settings })
  }

  getStoredState() {
    return Browser.storage.local.get('Pomodoro.State').then((data) => {
      return data['Pomodoro.State'] as PomodoroState
    }) as Promise<PomodoroState>
  }

  async isSettingsStorageInitialized() {
    return (await this.getStoredSettings()) !== undefined
  }

  async fillSettings() {
    // Initialize if not already done.
    if (!(await this.isSettingsStorageInitialized())) {
      await this.setStoredSettings(defaultSettings)
    }

    this.settings = await this.getStoredSettings()
  }

  start(startedAt = Date.now()) {
    if (this.state.isRunning) {
      return false
    }

    this.state.isRunning = startedAt

    this.emit('clock:start', this.getState())
    this.emit('state:change', this.getState())
  }

  goToBreak(isAutoStartEnabled: boolean) {
    this.state.isLargeBreak = this.isLargeBreakNeeded()
    this.state.isBreak = !this.state.isLargeBreak

    this.state.duration = this.state.isLargeBreak
      ? this.settings.longBreakDuration
      : this.settings.breakDuration

    this.pause()

    if (isAutoStartEnabled) {
      this.start()
    }
  }

  goToFocus(isAutoStartEnabled: boolean) {
    this.state.isBreak = false
    this.state.isLargeBreak = false
    this.state.duration = this.settings.focusDuration

    if (this.state.session < this.settings.sessions) {
      this.state.session++
    } else {
      return this.reset()
    }

    this.pause()

    if (isAutoStartEnabled) {
      this.start()
    }
  }

  pause() {
    if (this.state.isRunning) {
      this.state.isRunning = null
    }

    this.emit('state:change', this.getState())
  }

  reset() {
    this.state = {
      ...initialState,
      duration: this.settings.focusDuration,
    }

    this.emit('clock:reset', this.getState())
    this.emit('state:change', this.getState())
  }

  goToNextState() {
    if (this.isOnBreak()) {
      this.goToFocus(false) // Do not auto start focus.
    } else {
      this.goToBreak(this.settings.autoStartBreak)
    }

    this.emit('state:change', this.getState())
  }

  isLargeBreakNeeded() {
    return this.state.session === this.settings.sessions
  }

  isOnBreak() {
    return this.state.isBreak || this.state.isLargeBreak
  }

  getState(): PomodoroState {
    return {
      ...this.state,
      computed: this.getComputedState(),
    }
  }

  getComputedState(): PomodoroState['computed'] {
    return {
      isIdle: !this.state.isRunning && !this.isOnBreak(),
      isIdleBreak: !this.state.isRunning && this.isOnBreak(),
      isRunningBreak: !!this.state.isRunning && this.isOnBreak(),
      isRunningFocus: !!this.state.isRunning && !this.isOnBreak(),
    }
  }

  getSettings() {
    return this.settings
  }

  on(eventName: PomodoroEvents, callback: EventCallback): void {
    if (!this.events[eventName]) {
      this.events[eventName] = []
    }

    this.events[eventName].push(callback)
  }

  emit(
    eventName: PomodoroEvents,
    ...args: Parameters<typeof this.events[PomodoroEvents][number]>
  ): void {
    if (this.events[eventName]) {
      // eslint-disable-next-line n/no-callback-literal
      this.events[eventName].forEach((callback) => callback(...args))
    }
  }
}

export const PomodoroService = new Pomodoro()
