import apiClient from '@src/background/_internal/_api'
import { logger } from '@src/lib/errorReport'
import { useUserStorage } from '@src/v4/services/UsersService'
import { useMutation } from '@tanstack/react-query'
import Browser from 'webextension-polyfill'

import { type CurrentUser } from '@toggl/track-types'

type MutationContext = Partial<CurrentUser>

const UserUpdateService = {
  eventKey: 'UPDATE_USER_V4' as const,

  invoke: async (payload: Partial<CurrentUser>) => {
    // @ts-expect-error No overload matches this call // TODO: fix upstream
    await apiClient.me.update(payload)
  },

  dispatch(data: MutationContext): Promise<void> {
    return Browser.runtime.sendMessage({
      type: UserUpdateService.eventKey,
      payload: data,
    })
  },
}

type MutationData = Awaited<ReturnType<typeof UserUpdateService['invoke']>>

const useUserUpdateMutation = () => {
  const [, setUser] = useUserStorage()
  return useMutation<MutationData, Error, MutationContext>(
    (data) => UserUpdateService.dispatch(data),
    {
      onError: (error) => {
        return logger.error(error)
      },
      onSuccess: (_, payload) => {
        setUser((prev) => ({ ...prev, ...payload } as CurrentUser))
      },
    }
  )
}

export { UserUpdateService, useUserUpdateMutation }
