import apiClient from '@src/background/_internal/_api'

import { type WorkspacePreferences } from '@toggl/track-types'

import { BaseService } from '../lib/BaseService'
import { useBrowserStorageLocal } from '../lib/use-browser-storage'

class Service<K extends string> extends BaseService<WorkspacePreferences, K> {
  async invoke(wid: number, opts: { store?: boolean } = { store: false }) {
    const { data } = await apiClient.workspaces.preferences.fetch(wid)

    // Insert into storage
    if (opts.store) {
      await this.store(data)
    }

    return data
  }
}

const WSPreferencesService = new Service('FETCH_WS_PREFERENCES_V4')

type StorageData = Awaited<ReturnType<typeof WSPreferencesService['invoke']>>

const useWSPreferencesStorage = (initial: StorageData | null = null) => {
  return useBrowserStorageLocal<StorageData>(
    WSPreferencesService.storageKey,
    initial
  )
}

export { WSPreferencesService, useWSPreferencesStorage }
