import { logger } from '@src/lib/errorReport'
import Browser from 'webextension-polyfill'

interface HeapApiConfig {
  baseUrl: string
  defaultAppId: string
}

export interface HeapEvent {
  identity: string
  event: string
  timestamp?: string
  properties: Record<string, string | number | boolean | null>
}

class HeapAnalyticsClient {
  private config: HeapApiConfig

  constructor(config: HeapApiConfig) {
    this.config = config
  }

  private async sendRequest(endpoint: string, data: unknown) {
    const url = `${this.config.baseUrl}${endpoint}`

    logger.debug('Sending HEAP Request', url)

    try {
      const response = await fetch(url, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      })

      logger.debug('HEAP Request Sent, status:', response.status)

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`)
      }

      return response.text()
    } catch (error) {
      logger.debug('Failed to send HEAP Request', error.message)
    }

    return Promise.reject(new Error('Failed to send HEAP Request'))
  }

  track(event: HeapEvent) {
    return this.sendRequest('/track', {
      app_id: this.config.defaultAppId,
      identity: event.identity,
      event: event.event,
      timestamp: event.timestamp ?? new Date().toISOString(),
      properties: event.properties,
    })
  }

  dispatch(event: HeapEvent) {
    return Browser.runtime.sendMessage({
      type: 'HEAP_EVENT_TRACK',
      payload: event,
    })
  }
}

export const heapClient = new HeapAnalyticsClient({
  baseUrl: 'https://heapanalytics.com/api',
  defaultAppId: process.env.HEAP_APP_ID ?? '',
})
