import { onIconHandlerStateChangeListener } from '@src/background/icon-handler'
import { onWebsocketStateChangesListener } from '@src/background/websocket'
import { Db } from '@src/lib/db'
import { onRemindersStateChangeListener } from '@src/lib/reminders'
import { onStopAtDayEndStateChangesListener } from '@src/lib/stopTimeAtDayEnd'
import { onPomodoroV2StateChangesListener } from '@src/pomodoro/listener'
import { onWorkspaceListStateChangeListener } from '@src/v4/services/SelectedWorkspaceService'

Db.addStateChangeListener((changes, areaName) => {
  void onIconHandlerStateChangeListener(changes, areaName)
  void onRemindersStateChangeListener(changes, areaName)
  void onWebsocketStateChangesListener(changes, areaName)
  void onStopAtDayEndStateChangesListener(changes, areaName)
  void onWorkspaceListStateChangeListener(changes, areaName)
  void onPomodoroV2StateChangesListener(changes, areaName)
})
