import * as Browser from 'webextension-polyfill'

let workerProxyToken
/**
 * Get the session token used to authenticate messages between the content
 * script and worker proxy page. This value will change whenever the
 * background/event page unloads.
 */
function getProxyWorkerChannelToken() {
  if (!workerProxyToken) {
    const buffer = new Uint8Array(100)
    crypto.getRandomValues(buffer)
    let randomToken = ''
    for (let i = 0; i < buffer.length; ++i) {
      randomToken += buffer[i].toString(36)
    }
    workerProxyToken = randomToken
  }
  return workerProxyToken
}

Browser.runtime.onMessage.addListener((message: string) => {
  if (typeof message === 'string' && message.includes('worker_proxy')) {
    return Promise.resolve(getProxyWorkerChannelToken())
  }
})
