import { Db, type StateChangeListener } from '@src/lib/db'
import { logger } from '@src/lib/errorReport'
import { CurrentTEService } from '@src/v4/services/CurrentTEService'
import { NotificationService } from '@src/v4/services/NotificationService'
import { DateTime } from 'luxon'
import * as Browser from 'webextension-polyfill'

import { stopRunningTimeEntry } from './timeEntries'

const handleAlarm = async () => {
  const current = await CurrentTEService.invoke()

  if (current) {
    await stopRunningTimeEntry(current, null)
  }
}

export const configureStopAtDayEnd = async (time: string) => {
  logger.debug('stopTimeAtDayEnd::configureStopAtDayEnd:', time)
  let stopTime = DateTime.fromISO(time).set({ second: 0 })

  if (stopTime < DateTime.now()) stopTime = stopTime.plus({ days: 1 })

  await Browser.alarms.clear(StopAtDayEndAlarm.name)
  Browser.alarms.create(StopAtDayEndAlarm.name, {
    when: stopTime.toMillis(),
    periodInMinutes: 60 * 24,
  })
}

export const clearStopAtDayEndAlarm = async () => {
  logger.debug('stopTimeAtDayEnd::clearStopAtDayEndAlarm')

  await Browser.alarms.clear(StopAtDayEndAlarm.name)
}

export const StopAtDayEndAlarm = {
  name: 'STOP_AT_DAY_END',

  handler: async () => {
    try {
      await handleAlarm()
    } catch (error) {
      logger.error(error)
      await NotificationService.create('Something went wrong', error.message)
    }
  },

  async configure() {
    const stopAtDayEnd = await Db.get('stopAtDayEnd')
    const dayEndTime = await Db.get('dayEndTime')
    if (stopAtDayEnd) await configureStopAtDayEnd(dayEndTime)
    else await StopAtDayEndAlarm.clear()
  },

  async clear() {
    await Browser.alarms.clear(StopAtDayEndAlarm.name)
  },
}

export const onStopAtDayEndStateChangesListener: StateChangeListener = async (
  changes,
  areaName
) => {
  try {
    if (areaName === 'sync' && (changes.stopAtDayEnd || changes.dayEndTime)) {
      await StopAtDayEndAlarm.configure()
    }
  } catch (error) {
    logger.error(error, 'onStopAtDayEndStateChangesListener')
  }
}
