import { setInitialIconState } from '@src/background/icon-handler'
import {
  WEBSOCKET_ALARM_ID,
  WEBSOCKET_ALARM_RETRY_ID,
  checkSocketHealth,
  connectWebSocket,
} from '@src/background/websocket'
import { logger } from '@src/lib/errorReport'
import { scheduleFullFetch } from '@src/lib/fetch-handler'
import { idleListener } from '@src/lib/idle'
import {
  TRACKING_REMINDER_ALARM_ID,
  handleReminderAlarm,
} from '@src/lib/reminders'
import { StopAtDayEndAlarm } from '@src/lib/stopTimeAtDayEnd'
import Browser from 'webextension-polyfill'
import './background/index'

import { FullFetchAlarm } from './lib/full-fetch'
import { PomodoroService } from './pomodoro/PomodoroService'

// Reconnect when the background script starts
void connectWebSocket()

// these handlers need to be on top level
// https://developer.chrome.com/docs/extensions/develop/migrate/to-service-workers#event_listeners
Browser.alarms.onAlarm.addListener((alarm) => {
  // Log the alarm name and timestamp to the console
  logger.debug('SW::onAlarm', alarm.name, 'fired at:', alarm.scheduledTime)

  // Handle the alarm event
  if (alarm.name === WEBSOCKET_ALARM_ID) {
    void checkSocketHealth()
  } else if (alarm.name === WEBSOCKET_ALARM_RETRY_ID) {
    void connectWebSocket()
  } else if (alarm.name === TRACKING_REMINDER_ALARM_ID) {
    void handleReminderAlarm()
  } else if (
    alarm.name === FullFetchAlarm.name ||
    alarm.name === FullFetchAlarm.retryAlarmName
  ) {
    void FullFetchAlarm.handler()
  } else if (alarm.name === StopAtDayEndAlarm.name) {
    void StopAtDayEndAlarm.handler()
  }

  if (alarm.name === 'Pomodoro.Alarm') {
    PomodoroService.goToNextState()
  }
})

Browser.idle.onStateChanged.addListener(idleListener)

// Schedule the full fetch alarm with a delay to wait for websocket connection
void setInitialIconState()
FullFetchAlarm.configure()

Browser.runtime.onStartup.addListener(() => {
  scheduleFullFetch()
})

Browser.runtime.onInstalled.addListener(() => {
  scheduleFullFetch()
})
