import { logger } from '@src/lib/errorReport'
import Browser from 'webextension-polyfill'

import { PomodoroService } from './PomodoroService'

type Audio = { src: string; volume: number; audioLength: number }

const closePopups = async () => {
  try {
    const popups = await Browser.windows.getAll({ windowTypes: ['popup'] })
    await Promise.all(
      popups.map(
        async (popup) => popup.id && (await Browser.windows.remove(popup.id))
      )
    )
  } catch (error) {
    logger.error(error, 'sounds.ts - closePopups')
  }
}

const playSoundFallback = (audio: Audio) => {
  let url = chrome.runtime.getURL('src/content/audio/index.html')

  url += `?volume=${audio.volume}&src=${audio.src}&length=${audio.audioLength}`

  void Browser.windows.create({
    type: 'popup',
    focused: false,
    top: 0,
    left: 0,
    width: 1,
    height: 1,
    url,
  })
}

const playSoundOffscreen = async (args: Audio) => {
  try {
    if (!chrome.offscreen) {
      playSoundFallback({
        src: args.src,
        volume: args.volume,
        audioLength: args.audioLength,
      })
      return
    }

    if (!(await chrome.offscreen.hasDocument())) {
      await chrome.offscreen.createDocument({
        url: 'src/content/offscreen/index.html',
        reasons: ['AUDIO_PLAYBACK' as chrome.offscreen.Reason],
        justification: 'Play pomodoro alerts',
      })
    }

    await Browser.runtime.sendMessage({
      type: 'OFFSCREEN_PLAY_POMODORO_SOUND',
      payload: {
        src: args.src,
        volume: args.volume,
        audioLength: args.audioLength,
      },
    })
  } catch (error) {
    logger.error(error, 'playSoundV2')
  }
}

export const playPomodoroAlert = async () => {
  const setting = await PomodoroService.getStoredSettings()
  if (!setting.isAlarmEnabled) {
    return
  }
  await playSoundOffscreen({
    src: Browser.runtime.getURL('sounds/time_is_up_1.mp3'),
    audioLength: 2000,
    volume: setting.alarmVolumePercent,
  })
}

export const startTicking = async (playbackSeconds: number) => {
  const setting = await PomodoroService.getStoredSettings()
  if (!setting.isTickingEnabled) {
    return
  }
  await playSoundOffscreen({
    src: Browser.runtime.getURL('sounds/tick-tock.mp3'),
    audioLength: playbackSeconds * 1000,
    volume: setting.alarmVolumePercent,
  })
}

export const stopTicking = async () => {
  if (chrome.offscreen) {
    return Browser.runtime.sendMessage({
      type: 'OFFSCREEN_PLAY_POMODORO_SOUND_STOP',
    })
  }
  await closePopups()
}
