import { logger } from '@src/lib/errorReport'
import * as Browser from 'webextension-polyfill'

import { DEFAULT_SETTINGS, type LOCAL_STATE } from './constants'

type LocalState = typeof LOCAL_STATE

type StateChanges = Record<
  keyof (typeof DEFAULT_SETTINGS & typeof LOCAL_STATE),
  Browser.Storage.StorageChange
>

export type StateChangeListener = (
  changes: StateChanges,
  areaName: 'sync' | 'local'
) => Promise<void> | void

export class Db {
  static get<T extends keyof typeof DEFAULT_SETTINGS>(
    key: T
  ): Promise<typeof DEFAULT_SETTINGS[T]> {
    return Browser.storage.sync.get(key).then((result) => result[key])
  }

  static set<T extends keyof typeof DEFAULT_SETTINGS>(
    key: T,
    value: typeof DEFAULT_SETTINGS[T]
  ) {
    return Browser.storage.sync
      .set({ [key]: value })

      .catch((e) => logger.error(e, `Error attempting to save ${key}`))
      .finally(() => logger.debug('DB::Set', `${key} -> ${value}`))
  }

  static addStateChangeListener(onStateChanged: StateChangeListener) {
    Browser.storage.onChanged.addListener((changes, areaName) => {
      void onStateChanged(changes as StateChanges, areaName as 'sync' | 'local')
    })
  }

  static getLocal<T extends keyof LocalState>(key: T): Promise<LocalState[T]> {
    return Browser.storage.local
      .get(key as string)
      .then((result: LocalState) => result[key])
  }

  static setLocal<T extends keyof LocalState>(key: T, value: LocalState[T]) {
    return Browser.storage.local.set({ [key as string]: value })
  }

  static load(setting: keyof typeof DEFAULT_SETTINGS, defaultValue: any) {
    this.get(setting).then((value) => {
      if (typeof value === 'undefined') {
        value = defaultValue
        this.set(setting, value)
      }
    })
  }

  static loadAll() {
    for (const k in DEFAULT_SETTINGS) {
      if (DEFAULT_SETTINGS[k] !== undefined) {
        this.load(k as any, DEFAULT_SETTINGS[k])
      }
    }
  }
}
