import { logger } from '@src/lib/errorReport'
import { FullFetchAlarm } from '@src/lib/full-fetch'
import { PomodoroService } from '@src/pomodoro/PomodoroService'
import { ClientsListService } from '@src/v4/services/ClientsListService'
import { CurrentTEService } from '@src/v4/services/CurrentTEService'
import { ProjectsListService } from '@src/v4/services/ProjectsListService'
import { SelectedWorkspaceService } from '@src/v4/services/SelectedWorkspaceService'
import { TagsListService } from '@src/v4/services/TagsListService'
import { TasksListService } from '@src/v4/services/TasksListService'
import { TEListService } from '@src/v4/services/TEListService'
import { UsersService } from '@src/v4/services/UsersService'
import { WorkspacesListService } from '@src/v4/services/WorkspacesListService'
import { WSPreferencesService } from '@src/v4/services/WSPreferencesService'
import { debounce } from 'lodash'

import { type TimeEntryV9 } from '@toggl/track-types'
import * as util from '@toggl/track-util'

// Debounced handler to handle burst of events

const DEBOUNCE_INTERVAL = 1000

const debouncedFullFetch = debounce(async () => {
  await FullFetchAlarm.handler()
}, DEBOUNCE_INTERVAL)

const debouncedTaskFetch = debounce(async (fullSync?: boolean) => {
  await TasksListService.invoke(
    fullSync ? null : await TasksListService.since()
  )
}, DEBOUNCE_INTERVAL)

const debouncedClientFetch = debounce(async () => {
  await ClientsListService.invoke(await ClientsListService.since())
}, DEBOUNCE_INTERVAL)

const debouncedTagsFetch = debounce(async () => {
  await TagsListService.invoke(await TagsListService.since())
}, DEBOUNCE_INTERVAL)

const debouncedProjectFetch = debounce(async () => {
  const projects = await ProjectsListService.invoke(
    await ProjectsListService.since()
  )
  if (Object.keys(projects ?? {}).length) {
    await debouncedTaskFetch(true)
  }
}, DEBOUNCE_INTERVAL)

const debouncedWorkspacesFetch = debounce(async () => {
  await WorkspacesListService.invoke(await WorkspacesListService.since())
}, DEBOUNCE_INTERVAL)

const debouncedWSPreferencesFetch = debounce(async (wid: number) => {
  await WSPreferencesService.invoke(wid, { store: true })
}, DEBOUNCE_INTERVAL)

const debouncedPomodoroReset = debounce(async (data: TimeEntryV9) => {
  const current = await CurrentTEService.retrieve()

  if (current?.id === data.id) {
    const isDataRunning = util.timer.isTimeEntryRunning(data)

    if (!isDataRunning) {
      PomodoroService.reset()
    }
  }
}, DEBOUNCE_INTERVAL)

// callbacks from WS and FCM

async function handleEvent(data: any) {
  switch (data.model) {
    case 'time_entry': {
      const currentTE = await CurrentTEService.retrieve()

      // Needs to be debouced to avoid burst of events
      debouncedPomodoroReset(data.data)
      // {"action":"update","data":{"id":3578357098,"wid":356965,"duration":-1724764671,"duration_diff":0,"pid":null,"tid":null,"tag_ids":null,"tags":[],"description":"test 12345678910","billable":false,"duronly":true,"start":"2024-08-27T13:17:51Z","stop":null,"shared_with":null,"at":"2024-08-27T13:19:23.196799Z"},"model":"time_entry"}
      if (
        util.timer.isTimeEntryRunning(data.data as TimeEntryV9) ||
        currentTE?.id === data.data.id
      ) {
        return CurrentTEService.store(
          Object.assign(data.data, {
            workspace_id: data.data.wid,
            project_id: data.data.pid,
            task_id: data.data.tid,
          })
        )
      }

      if (!util.timer.isTimeEntryRunning(data.data as TimeEntryV9)) {
        if (data.action === 'delete') {
          await TEListService.deleteFromStore(data.data.id)
        } else {
          await TEListService.updateStore(data.data)
        }
      }
      break
    }
    case 'client':
      return debouncedClientFetch()

    case 'group_entity_roles':
    case 'user_entity_roles':
      if (data.data.project_id) {
        return debouncedProjectFetch()
      } else {
        return debouncedFullFetch()
      }

    case 'workspace': {
      const wid = data.data.id as number
      const user = await UsersService.retrieve()
      const calls = [debouncedWorkspacesFetch()]

      // Fetch just in case the default workspace is updated
      if (user && (await SelectedWorkspaceService.retrieve()) === wid) {
        calls.push(debouncedWSPreferencesFetch(wid))
      }

      return Promise.all(calls)
    }

    case 'project':
    case 'project_user':
      return debouncedProjectFetch()

    case 'tag':
      return debouncedTagsFetch()

    case 'task':
      return debouncedTaskFetch()

    default:
      logger.debug('Unidentified websocket event model: ' + data.model)
  }
}

export async function onWebSocketDataReceived(data: any) {
  logger.debug('WS::onDataReceived', data)
  try {
    await handleEvent(data)
  } catch (error) {
    logger.error(error, 'Error in onWebSocketDataReceived')
  }
}
