import apiClient from '@src/background/_internal/_api'
import { with403Handler } from '@src/shared/utils/with403Handler'

import { type OrganizationFeatures } from '@toggl/track-types'

import { BaseService } from '../lib/BaseService'
import { useBrowserStorageLocal } from '../lib/use-browser-storage'

class Service<K extends string> extends BaseService<OrganizationFeatures[], K> {
  invoke = with403Handler(async () => {
    const { data } = await apiClient.me.features.organization.list()

    // Insert into storage
    await this.store(data)

    return data
  })
}

const OrgFeaturesService = new Service('FETCH_ORG_FEATURES_V4')

type StorageData = Awaited<ReturnType<typeof OrgFeaturesService['invoke']>>

const useOrgFeaturesStorage = (initial: StorageData | null = null) => {
  return useBrowserStorageLocal<StorageData>(
    OrgFeaturesService.storageKey,
    initial
  )
}

export { OrgFeaturesService, useOrgFeaturesStorage }
