import { type Mutable } from '@src/types'
import { ProjectsListService } from '@src/v4/services/ProjectsListService'
import { TagsListService } from '@src/v4/services/TagsListService'
import { type MessageFrom, type MutableTimeEntryV9 } from 'src/typdef'

import { type TimeEntryRequest, type TimeEntryV9 } from '@toggl/track-types'

import { Db } from './db'

export const updateDefaultProjects = async (
  from: MessageFrom,
  projectId: number | null | undefined
) => {
  const rememberPer = await Db.get('rememberProjectPer')

  if (rememberPer !== 'none') {
    const defaultProjects = (await Db.getLocal('defaultProjects')) || '{}'

    await Db.setLocal('defaultProjects', {
      ...defaultProjects,
      [rememberPer === 'integration' ? from.origin! : from.url!]:
        projectId ?? undefined,
    })
  }
}

// TODO: This needs attention and possibly a refactor.
export const setProject = async (te: TimeEntryRequest, from: MessageFrom) => {
  const entry = te as Mutable<TimeEntryRequest> & {
    project_id?: number
    workspace_id?: number
    billable: boolean
  }

  const rememberPer = await Db.get('rememberProjectPer')
  const defaultProjectId = await Db.get('defaultProject')

  if (from.type === 'contentScript') {
    if (rememberPer !== 'none' && !entry.project_id) {
      const defaultProjects = (await Db.getLocal('defaultProjects')) || {}

      const projects = await ProjectsListService.retrieve()

      entry.project_id =
        rememberPer === 'integration'
          ? defaultProjects[from.origin!]
          : defaultProjects[from.url!]

      if (projects) {
        entry.billable =
          entry.billable || projects[entry.project_id ?? '']?.billable

        entry.workspace_id =
          projects[entry.project_id ?? '']?.workspace_id ?? entry.workspace_id
      }
    }
  }

  if (!entry.project_id && defaultProjectId) {
    const projects = await ProjectsListService.retrieve()

    if (projects) {
      const defaultProject = projects[defaultProjectId]
      entry.project_id = defaultProjectId
      entry.workspace_id = defaultProject.workspace_id
      entry.billable = entry.billable || projects[defaultProjectId].billable
    }
  }

  return entry
}

export const ensureSameWorkspace = async (
  te: TimeEntryV9 | TimeEntryRequest
) => {
  const entry = Object.assign({}, te) as MutableTimeEntryV9

  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  delete entry.pid
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  delete entry.tid
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  delete entry.wid
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  delete entry.uid

  if (entry.project_id) {
    const projects = await ProjectsListService.retrieve()

    if (projects) {
      const project = projects[entry.project_id]
      entry.workspace_id = project.workspace_id
    }
  }

  if (entry.tag_ids?.length) {
    const tags = await TagsListService.retrieve()

    if (tags) {
      entry.tags = entry.tag_ids.map((id) => tags[id]?.name)
    }
  } else {
    entry.tags = []
  }

  delete entry.tag_ids

  return entry as TimeEntryV9
}
