import apiClient from '@src/background/_internal/_api'
import { with403Handler } from '@src/shared/utils/with403Handler'

import { type TimeEntry, type TimeEntryV9 } from '@toggl/track-types'

import { BaseService } from '../lib/BaseService'
import { useBrowserStorageLocal } from '../lib/use-browser-storage'

class Service<K extends string> extends BaseService<
  Record<number, TimeEntryV9>,
  K
> {
  invoke = with403Handler(
    async (opts: { store?: boolean } = { store: false }) => {
      const { data } = await apiClient.me.timeEntries.fetch('', '')

      const byIds = (data as unknown as TimeEntryV9[]).reduce((acc, te) => {
        return { ...acc, [te.id]: te }
      }, {} as Record<number, TimeEntryV9>)

      if (opts.store) {
        // Insert into storage
        await this.store(byIds)
      }

      return byIds
    }
  )

  async updateStore(data: TimeEntry) {
    let timeEntries = await this.retrieve()
    timeEntries = {
      ...timeEntries,
      // convert TimeEntry to TimeEntryV9
      [data.id]: {
        ...data,
        project_id: data.pid,
        workspace_id: data.wid,
        task_id: data.tid,
      },
    }
    await this.store(timeEntries)
  }

  async deleteFromStore(id: number) {
    const timeEntries = await this.retrieve()
    delete timeEntries?.[id]
    await this.store(timeEntries)
  }
}

const TEListService = new Service('FETCH_TE_LIST_V4')

type StorageData = Awaited<ReturnType<typeof TEListService['invoke']>>

const useTEListStorage = (initial: StorageData | null = null) => {
  return useBrowserStorageLocal<StorageData>(TEListService.storageKey, initial)
}

export { TEListService, useTEListStorage }
