import apiClient from '@src/background/_internal/_api'
import { BaseService } from '@src/v4/lib/BaseService'

import { type Organization } from '@toggl/track-types'

import { useBrowserStorageLocal } from '../lib/use-browser-storage'

class ListService<K extends string> extends BaseService<Organization[], K> {
  async invoke() {
    const { data } = await apiClient.me.organizations.list()
    await this.store(data)
    return data
  }
}

const OrganizationsListService = new ListService('FETCH_ORGANIZATIONS_V4')

type StorageData = Awaited<
  ReturnType<typeof OrganizationsListService['invoke']>
>

const useOrganizationsListStorage = (initial: StorageData | null = null) => {
  return useBrowserStorageLocal<StorageData>(
    OrganizationsListService.storageKey,
    initial
  )
}

export { OrganizationsListService, useOrganizationsListStorage }
