import { type StateChangeListener } from '@src/lib/db'
import { logger } from '@src/lib/errorReport'
import { CurrentTEService } from '@src/v4/services/CurrentTEService'
import { UsersService } from '@src/v4/services/UsersService'
import Browser from 'webextension-polyfill'

export async function setInitialIconState() {
  const currentTE = await CurrentTEService.retrieve()
  await changeIconState(currentTE && currentTE?.duration < 0)

  const user = await UsersService.retrieve()
  await setSignedInStatus(!!user?.id)
}

async function changeIconState(isActive?: boolean | null) {
  await Browser.action.setIcon({
    path: isActive
      ? { '19': 'images/icon-19.png', '38': 'images/icon-38.png' }
      : { '19': 'images/inactive-19.png', '38': 'images/inactive-38.png' },
  })
}

async function setSignedInStatus(isSignedIn: boolean) {
  if (isSignedIn) {
    await Browser.action.setBadgeText({ text: '' })
  } else {
    await Promise.all([
      Browser.action.setBadgeText({ text: 'x' }),
      // eslint-disable-next-line @toggl/no-color-literals
      Browser.action.setBadgeTextColor({ color: '#fff' }),
      // eslint-disable-next-line @toggl/no-color-literals
      Browser.action.setBadgeBackgroundColor({ color: '#f00' }),
    ])
  }
}

export const onIconHandlerStateChangeListener: StateChangeListener = async (
  changes,
  areaName
) => {
  try {
    // Handle the color of the icon based on the current time entry state.
    if (areaName === 'local' && CurrentTEService.storageKey in changes) {
      const currentTE = changes[CurrentTEService.storageKey].newValue
      const isActive = currentTE && currentTE?.duration < 0

      await changeIconState(isActive)
    }

    // Handle the icon badge based on the current user state.
    if (areaName === 'local' && UsersService.storageKey in changes) {
      // Can be null | undefined
      const isSignedIn = changes[UsersService.storageKey].newValue

      await setSignedInStatus(isSignedIn)
    }
  } catch (error) {
    logger.error(error, 'onIconHandlerStateChangeListener')
  }
}
