/**
 * Delay full fetch by 5 seconds to wait for WS authentication to avoid
 * duplicate api calls
 */
import { logger } from '@src/lib/errorReport'
import { FullFetchAlarm } from '@src/lib/full-fetch'
import { doFetchUsingSince } from '@src/lib/since-fetch'

let timeoutId: null | NodeJS.Timeout
export let isWebsocketActive = false

export function scheduleFullFetch() {
  // For new users so they see loading instead of login screen
  FullFetchAlarm.updateStatus({
    loading: true,
  })
  timeoutId = setTimeout(() => {
    timeoutId = null
    void FullFetchAlarm.handler()
  }, 5000)
}

export function onWebsocketAuthenticated() {
  ;(async () => {
    try {
      logger.debug('WS::onAuthenticated')
      if (timeoutId) {
        clearTimeout(timeoutId)
        await FullFetchAlarm.handler()
      } else {
        await doFetchUsingSince()
      }
      isWebsocketActive = true
    } catch (error) {
      logger.error(error, 'WS::onAuthenticated')
    }
  })()
}

export function onWebsocketDisconnected() {
  logger.debug('WS::onWebsocketDisconnected')
  isWebsocketActive = false
}
