import { logger } from '@src/lib/errorReport'
import Browser from 'webextension-polyfill'

export const configure = async (tabId, tab) => {
  if (tab.url === undefined) return

  try {
    const response = await Browser.tabs.sendMessage(tabId, {
      type: 'ping-tiny-button',
    })
    if (response.type === 'pong-tiny-button') return
  } catch (e) {
    logger.debug(e)
  }

  Browser.scripting
    .executeScript({
      target: { tabId },
      files: ['src/tinyButton.js'],
    })
    .catch((e) => logger.error(e))
}
