import type { StateChangeListener } from '@src/lib/db'
import { BaseService } from '@src/v4/lib/BaseService'
import { useBrowserStorageLocal } from '@src/v4/lib/use-browser-storage'
import { UsersService, useUserStorage } from '@src/v4/services/UsersService'
import { WorkspacesListService } from '@src/v4/services/WorkspacesListService'

type StorageData = number | null | undefined

class Service<K extends string> extends BaseService<StorageData, K> {
  async retrieve() {
    const wsId = await super.retrieve()
    return wsId ?? (await UsersService.retrieve())?.default_workspace_id
  }
}

export const SelectedWorkspaceService = new Service('SELECTED_WORKSPACE_V4')

export const useSelectedWorkspaceIdStorage = () => {
  const [user] = useUserStorage()
  const [selectedWS, ...args] = useBrowserStorageLocal<StorageData>(
    SelectedWorkspaceService.storageKey,
    user?.default_workspace_id ?? undefined
  )
  return [selectedWS ?? user?.default_workspace_id, ...args] as const
}

export const onWorkspaceListStateChangeListener: StateChangeListener = async (
  changes,
  areaName
) => {
  if (areaName === 'local' && WorkspacesListService.storageKey in changes) {
    const workspaces = changes[WorkspacesListService.storageKey].newValue
    if (
      workspaces &&
      !workspaces[(await SelectedWorkspaceService.retrieve()) ?? '']
    ) {
      // Reset to default workspace if the selected workspace is not in the list or is deleted
      await SelectedWorkspaceService.clear()
    }
  }
}
